import React from 'react';

import { styled } from '@lapilli-ui/styles';

import Icon from '../../icon';

const CheckboxWrapper = styled('div')(({ theme }) => ({
  display: 'flex',
  zIndex: '3',
  '&.disabled': {
    pointerEvents: 'none!important',
    opacity: '0.5'
  },
  '& a': {
    margin: '2px 3px 0 0',
    width: '22px',
    height: '22px',
    zIndex: '2',
    lineHeight: 'normal',
    padding: 0,
    position: 'relative',
    borderStyle: 'solid',
    textDecoration: 'none',
    borderWidth: '1px',
    display: 'inline-block'
  },
  '&.admin': {
    a: {
      backgroundColor: '#fff',
      borderColor: '#CFCFCF',
      borderRadius: '5px'
    },
    '&.admin.checked a': {
      backgroundColor: 'rgb(25, 107, 222)',
      borderColor: 'rgb(25, 107, 222)'
    }
  },
  '&.frontend, &.preview': {
    a: {
      borderColor: `${theme.forms.borderColor}!important`,
      backgroundColor: `${theme.forms.backgroundColor}!important`,
      color: `${theme.forms.text}!important`,
      borderRadius: `${theme.forms.borderRadius}px!important`
    }
  },
  '& a:hover': {
    cursor: 'pointer'
  },
  '& a .yith-sales-icon--customcheck': {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    height: '100%',
    zIndex: '1'
  }
}));
const Checkbox = ({
  id,
  label = false,
  checked = false,
  checkColor = '#A1C746',
  name = '',
  disabled = false,
  onChange,
  context = 'preview',
  ...other
}) => {
  return (
        <CheckboxWrapper className={`yith-sales-checkbox ${disabled ? 'disabled' : ''} ${context} ${checked ? 'checked' : ''}`}>
            <input id={id} type="hidden" value={checked ? 'yes' : 'no'} name={name} onChange={onChange} {...other} />
            <a onClick={onChange}>
                {checked && <Icon icon="CustomCheck" size='20' fillColor={ checkColor} sx={{ marginLeft: '1px' }}/>}
            </a>
            {label && <label htmlFor={id}>{label}</label>}
        </CheckboxWrapper>
  );
};

export default Checkbox;
