import React, { useEffect, useLayoutEffect, useReducer, useRef, useState } from 'react';

import { Box, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { cartURL } from '../../../common/config';
import Icon from '../../../common/icon';
import { getNoProductLabel, isEmpty } from '../../../common/utils/common';
import { getProductAttributes } from '../../../common/utils/data';
import { getDiscountedPrice } from '../../../common/utils/numbers';
import AddToCartProductListButton from '../add-to-cart-product-list-button';
import BoxWithInfo from '../box-with-info';
import ProductListItem from '../product-list-item';
import ProductListTotals from '../product-list-totals';
import SimpleButton from '../simple-button';
import { VerticalScrollableContentStyled } from '../styles/VerticalScrollableContent.styled';

function init (initialState) {
  return {
    productList: initialState.productList,
    productsSelected: initialState.productsSelected,
    productsAttributes: initialState.productsAttributes
  };
};

function scanReducer (state, action) {
  switch (action.type) {
    case 'initProductList':
      return { ...state, productList: [...action.payload] };
    case 'initProductSelected':
      return { ...state, productsSelected: [...action.payload] };
    case 'initProductsAttributes':
      return { ...state, productsAttributes: [...action.payload] };
    case 'reset':
      return init(action.payload);
  }
}

const ProductListStyled = styled(Stack)(({ theme }) => ({
  '& .yith-sales-product-list-item:last-child': {
    marginBottom: '0!important'
  },
  '& .with-scroll': {
    marginBottom: '0!important'
  }
}));
const AllDoneMessageStyled = styled(Stack)(({ theme }) => ({
  '&': {
    background: '#FFFFFF',
    boxShadow: '3px 2px 10px 5px rgba(184, 210, 224, 0.25)',
    minHeight: '80px',
    padding: '0 24px'
  }
}));
const ProductList = ({
  products,
  productOpts,
  numProductToShow = -1,
  basePrice = 0,
  showTotals = false,
  showNoThanks = false,
  handleNoThanks,
  showFreeLabel = false,
  maxProductToSelect = -1,
  addToCartClassName = '',
  canAddToCart = true,
  isAjax = true,
  showViewCartButton = false,
  showAllDoneMessage = false,
  context = 'preview',
  onSubmit
}) => {
  if (!products || products.length === 0) {
    const noResultLabel = getNoProductLabel(productOpts.productType);
    return (
            <BoxWithInfo info={noResultLabel} justify='center' iconSize={20} icon='InformationCircleIcon'
                         bgColor='#fff' color='#000' borderColor="#cacaca" margin='0 auto'/>
    );
  }
  const {
    applyDiscount,
    discountValue,
    discountType,
    addToCartLabel,
    showSaving,
    savingColor
  } = productOpts;
  const showDiscount = applyDiscount === 'yes' && discountValue > 0;
  let totalPrices = parseFloat(basePrice);
  let totalDiscountedPrices = parseFloat(basePrice);
  const verticalScrollRef = useRef();
  const [hasScroll, setHasScroll] = useState(false);
  const windowGap = context === 'preview' ? 450 : 450;
  const [verticalScroll, setVerticalScroll] = useState(window.innerHeight - windowGap);
  const [showAllDone, setShowAllDone] = useState(false);
  const checkScroll = () => {
    hasScroll !== (verticalScrollRef.current.scrollHeight > verticalScroll) && setHasScroll(!hasScroll);
  };
  const handleResize = () => {
    setVerticalScroll(window.innerHeight - windowGap);
    checkScroll();
  };

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    checkScroll();
  });

  const _initializeProductList = () => {
    const myProductList = [];

    for (const index in products) {
      const product = products[index];
      if (numProductToShow === -1 || index < numProductToShow) {
        myProductList.push(product);
      }
    }

    return myProductList;
  };
  const _initializeProductSelectedList = (productList) => {
    const allChecked = maxProductToSelect === -1 && productOpts.checkedByDefault === 'yes';
    const selected = [];
    let totalItemsChecked = 0;
    for (const index in productList) {
      const product = productList[index];
      const canAdded = allChecked || (maxProductToSelect !== -1 && totalItemsChecked < maxProductToSelect);
      if (!canAdded) {
        break;
      }
      selected.push(product.id);
      totalItemsChecked++;
    }
    return selected;
  };
  const _initializeProductsAttributes = (productList) => {
    const productsAttributes = [];
    for (const index in productList) {
      const product = productList[index];

      if (product.type === 'variable' && !isEmpty(product.attributes)) {
        const attributes = getProductAttributes(product);

        productsAttributes.push({ id: product.id, attributes });
      }
    }
    return productsAttributes;
  };
  const _initializeDefaultStatus = () => {
    const productList = _initializeProductList();
    const productsSelected = _initializeProductSelectedList(productList);
    const productsAttributes = _initializeProductsAttributes(productList);
    return { productList, productsSelected, productsAttributes };
  };
  const initialState = _initializeDefaultStatus();
  const [addToCartDisabled, setAddToCartDisabled] = useState(true);
  const [responsiveClass, setResponsiveClass] = useState('');
  const [state, dispatch] = useReducer(scanReducer, initialState, init);
  const { productList, productsSelected, productsAttributes } = state;
  useEffect(() => {
    dispatch({ type: 'reset', payload: _initializeDefaultStatus() });
  }, [numProductToShow, maxProductToSelect, productOpts.checkedByDefault]);

  useEffect(() => {
    const canAddToCart = () => {
      let canAddToCart = !isAjax;
      if (productsSelected.length > 0) {
        canAddToCart = true;
        for (const index in productsSelected) {
          const productID = productsSelected[index];
          const productInList = productList.filter(product => product.id === productID);
          if (productInList.length) {
            const product = productInList[0];
            if (product.type === 'variable') {
              if (product?.variationSelected && !isEmpty(product.variationSelected)) {
                const variation = product.variationSelected;
                if (!variation.is_purchasable || !variation.is_in_stock) {
                  canAddToCart = false;
                }
              } else {
                canAddToCart = false;
              }
            } else {
              if (!product.purchasable || !product.is_in_stock) {
                canAddToCart = false;
              }
            }
          }
          if (!canAddToCart) {
            break;
          }
        }
      }
      setAddToCartDisabled(!canAddToCart);
    };
    canAddToCart();
  }, [productsSelected, productList]);

  useEffect(() => {
    if (showAllDoneMessage) {
      const showMessage = productsSelected.length === parseInt(maxProductToSelect);
      setShowAllDone(showMessage);
    }
  }, [productsSelected]);
  const isProductSelected = (productID) => {
    return productsSelected.indexOf(productID) > -1;
  };

  const handleProductSelected = (productToCheck, isChecked) => {
    if (isChecked) {
      if (productsSelected.indexOf(productToCheck) === -1) {
        if (parseInt(maxProductToSelect) === 1) {
          const newItem = [productToCheck];
          dispatch({ type: 'initProductSelected', payload: newItem });
        } else {
          productsSelected.push(productToCheck);
          dispatch({ type: 'initProductSelected', payload: productsSelected });
        }
      }
    } else {
      if (productsSelected.indexOf(productToCheck) > -1) {
        const newItems = productsSelected.filter(productID => productID !== productToCheck);

        dispatch({ type: 'initProductSelected', payload: newItems });
      }
    }
  };

  const onFoundVariation = (productID, variation) => {
    let newList = [...productList];
    newList = newList.map(product => {
      const newItem = { ...product };
      if (newItem.id === productID) {
        newItem.variationSelected = variation;
      }
      return newItem;
    });
    dispatch({ type: 'initProductList', payload: newList });
  };

  const getProductAttributesByID = (productID) => {
    const attributes = productsAttributes.filter(productAttributes => productAttributes.id === productID);
    return attributes.length ? attributes[0].attributes : [];
  };
  const handleSelectAttributes = (productID, attributes) => {
    let newList = [...productsAttributes];
    newList = newList.map(product => {
      const newItem = { ...product };
      if (newItem.id === productID) {
        newItem.attributes = attributes;
      }
      return newItem;
    });
    dispatch({ type: 'initProductsAttributes', payload: newList });
  };
  const handleAddToCart = (e) => {
    if (isAjax) {
      e.preventDefault();
      e.stopPropagation();

      const data = [];
      for (const index in productsSelected) {
        const productID = productsSelected[index];
        const product = productList.filter(productInList => productInList.id === productID);
        if (product.length) {
          const element = {
            product_id: product[0].id,
            variation_id: product[0].type === 'variable' && product[0]?.variationSelected ? product[0].variationSelected.variationID : '',
            variations: product[0].type === 'variable' && product[0]?.variationSelected ? product[0].variationSelected.selectedAttributes : [],
            quantity: 1
          };

          data.push(element);
        }
      }
      if (typeof onSubmit === 'function') {
        onSubmit(e, data);
      }
    }
  };

  const ref = useRef(null);

  useLayoutEffect(() => {
    const wrapper = ref.current.closest('.yith-sales-frequently-bought-together');
    if (wrapper) {
      const width = wrapper.offsetWidth;

      if (width < 400) {
        if (width >= 300 && width <= 399) {
          setResponsiveClass('responsive-300');
        }
        if (width >= 200 && width <= 299) {
          setResponsiveClass('responsive-200');
        }
      }
    }
  }, []);

  return (
        <ProductListStyled direction='column' spacing={2} ref={ref} sx={{ width: '100%' }}>
            <VerticalScrollableContentStyled ref={verticalScrollRef} className={hasScroll ? 'with-scroll' : ''}
                                             maxHeight={`${verticalScroll}px`}>
                <Box className="yith-sales-products-list" sx={{ paddingRight: '5px', marginTop: '1px' }}>
                    {
                        productList.map((product, index) => {
                          const isSelected = isProductSelected(product.id);
                          if (isSelected) {
                            let canAddPrice = true;
                            if (product.type === 'variable') {
                              if (product?.variationSelected && !isEmpty(product.variationSelected)) {
                                product.regular_price = product.variationSelected.regular_price;
                              } else {
                                canAddPrice = false;
                              }
                            }
                            if (canAddPrice) {
                              let discountedPrice = parseFloat(product.regular_price);
                              totalPrices += parseFloat(discountedPrice);

                              if (showDiscount) {
                                discountedPrice = getDiscountedPrice(product.regular_price, discountValue, discountType);
                                totalDiscountedPrices += parseFloat(discountedPrice);
                              }
                            }
                          }
                          if (showViewCartButton && !isSelected) return '';
                          return (
                                <ProductListItem
                                    className={responsiveClass}
                                    key={product.id}
                                    product={product}
                                    productAttributes={getProductAttributesByID(product.id)}
                                    context={context}
                                    options={productOpts}
                                    showFreeLabel={showFreeLabel}
                                    checkedByDefault={isSelected}
                                    showCheckbox={!showViewCartButton}
                                    canBeChecked={maxProductToSelect <= 1 || isSelected || productsSelected.length < maxProductToSelect}
                                    onSelectItem={handleProductSelected}
                                    onFoundVariation={onFoundVariation}
                                    onSelectAttributes={handleSelectAttributes}
                                    name={`yith_sales_product_list[${index}]`}
                                />
                          );
                        })
                    }
                </Box>
            </VerticalScrollableContentStyled>
            {showTotals && totalPrices > 0 && (
                <ProductListTotals
                    totalDiscountedPrices={totalDiscountedPrices}
                    totalPrices={totalPrices}
                    showDiscount={showDiscount}
                    showSaving={showSaving}
                    savingColor={savingColor}
                />
            )
            }
          {showAllDoneMessage && showAllDone && !showViewCartButton && (
              <AllDoneMessageStyled align='center' direction='column' justify='center'>
                <Stack align='center' direction='row' spacing={1}>
                  <Icon icon='CheckIcon' sx={{ color: '#A1C746' }} size={20}/>
                  <Typography sx={{ fontWeight: 600, fontSize: '12px' }}>{__('You\'ve chosen all your gifts!', 'wonder-cart')}</Typography>
                </Stack>
                <Typography sx={{ fontSize: '12px' }}>{__('To pick a different one  remove a gift from your list', 'wonder-cart')}</Typography>
              </AllDoneMessageStyled>
          )}
            <Box sx={{ zIndex: '10', textAlign: 'center' }}>
                {
                    !showViewCartButton
                      ? (
                            <>
                                <AddToCartProductListButton label={addToCartLabel} className={addToCartClassName}
                                                            context={context}
                                                            isDisabled={addToCartDisabled || !canAddToCart}
                                                            onClick={handleAddToCart}/>
                                {showNoThanks && <SimpleButton
                                    className="yith-sales-nothanks-button"
                                    label={__('No, thanks', 'wonder-cart')}
                                    onClick={handleNoThanks}
                                    sx={{
                                      color: '#0079CA',
                                      fontSize: '1em',
                                      '&:hover': {
                                        color: '#0079CA'
                                      }
                                    }
                                    }
                                />}
                            </>
                        )
                      : (
                            <>
                                <SimpleButton href={cartURL}
                                              label={__('View cart & checkout', 'wonder-cart')}
                                              sx={{
                                                display: 'block',
                                                width: '100%',
                                                textAlign: 'center',
                                                textDecoration: 'none',
                                                marginBottom: '15px',
                                                boxSizing: 'border-box',
                                                backgroundColor: 'var(--wp--preset--color--primary)',
                                                borderRadius: 'var(--wp--custom--button-border-radius)',
                                                color: 'var(--wp--preset--color--base)',
                                                fontSize: 'var(--wp--preset--font-size--small)',
                                                paddingTop: '0.5rem',
                                                paddingRight: '1rem',
                                                paddingBottom: '0.5rem',
                                                paddingLeft: '1rem',
                                                '&:hover': {
                                                  textDecoration: 'none',
                                                  backgroundColor: 'var(--wp--preset--color--primary)',
                                                  color: 'var(--wp--preset--color--base)'
                                                }
                                              }}/>
                                <SimpleButton
                                    onClick={handleNoThanks}
                                    label={__('Keep Shopping', 'wonder-cart')}
                                    sx={{
                                      display: 'block',
                                      width: '100%',
                                      textAlign: 'center',
                                      textDecoration: 'none',
                                      marginBottom: '15px',
                                      backgroundColor: '#fff',
                                      border: '1px solid #707070',
                                      boxSizing: 'border-box',
                                      borderRadius: 'var(--wp--custom--button-border-radius)',
                                      color: '#707070',
                                      fontSize: 'var(--wp--preset--font-size--small)',
                                      paddingTop: '0.5rem',
                                      paddingRight: '1rem',
                                      paddingBottom: '0.5rem',
                                      paddingLeft: '1rem',
                                      '&:hover': {
                                        textDecoration: 'none',
                                        backgroundColor: '#707070',
                                        color: '#fff'
                                      }
                                    }}
                                />
                            </>
                        )
                }
            </Box>
        </ProductListStyled>
  );
};
export default ProductList;
