import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

import { shuffle } from '../../utils/common';
import { yithSalesApiFetch } from '../basic-fetch';

const restBase = 'products';

const getProductsById = async (termIds, fetchWithEmptyTerms, fetchWithBQ, context = 'view') => {
  if (Array.isArray(termIds)) {
    termIds = termIds.join(',');
  }
  let query = `/${restBase}`;
  if (termIds !== '') {
    query += '?include=' + termIds;
    query += '&context=' + context;
    const data = await fetchWithBQ(query);
    let dataToReturn = [];
    if (data.error) return { error: data.error };
    dataToReturn = [...data.data];

    return { data: dataToReturn };
  }

  return fetchWithEmptyTerms ? await fetchWithBQ(query) : { data: [] };
};
const getExcludedProducts = async (exclusion, fetchWithBQ, context = 'view') => {
  let query = `/${restBase}`;
  query += '?context=' + context;
  if (exclusion?.ids && exclusion.ids.length > 0) {
    if (exclusion.type === 'products') {
      query += `&exclude=${exclusion.ids}`;
    } else {
      query += `&categories_exclude=${exclusion.ids}`;
    }

    return await fetchWithBQ(query);
  }
  return { data: [] };
};
const getFeaturedProducts = async (fetchWithBQ, context = 'view') => {
  const query = `/${restBase}?featured=true&context=${context}`;

  return await fetchWithBQ(query);
};
const getBestSellerProducts = async (fetchWithBQ, context = 'view') => {
  const query = `/${restBase}?orderby=popularity&context=${context}`;
  return await fetchWithBQ(query);
};
const getProductsByCategories = async (categoryIds, fetchWithBQ, context = 'view') => {
  let productsByCategories = [];
  for (const index in categoryIds) {
    const tmpData = await fetchWithBQ(`/${restBase}?category=${categoryIds[index]}&context=${context}`);

    if (tmpData.error) return { error: tmpData.error };

    const ids = [];
    for (const i in tmpData.data) {
      const product = tmpData.data[i];
      ids.push(product.id);
    }
    productsByCategories = [...productsByCategories, ...ids];
  }

  return productsByCategories.length > 0 ? await getProductsById(productsByCategories, true, fetchWithBQ, context) : { data: [] };
};
const getProductsByProperty = async (dataToProcess, property, fetchWithBQ, context = 'view') => {
  let productIds = [];

  for (const i in dataToProcess) {
    const product = dataToProcess[i];
    productIds = [...productIds, ...product[property]];
  }
  productIds = productIds.filter((productId, index, self) => {
    return self.indexOf(productId) === index;
  });
  if (productIds.length > 0) {
    productIds = shuffle(productIds);
    if (productIds.length > 10) {
      productIds = productIds.slice(0, 10);
    }
    return await getProductsById(productIds, true, fetchWithBQ, context);
  }
  return { data: [] };
};
export const yithApiProductSlice = createApi({
  reducerPath: 'yithApiProduct',
  baseQuery: fetchBaseQuery({
    baseUrl: 'yith_sales/v1',
    fetchFn: yithSalesApiFetch,
    tagTypes: ['ProductTerm']
  }),
  endpoints: (builder) => ({
    getProducts: builder.query({
      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        const { termType, termIds, fetchWithEmptyTerms = true, context } = args;
        const data = [];
        if (termType === 'products') {
          return await getProductsById(termIds, fetchWithEmptyTerms, fetchWithBQ, context);
        }
        return { data };
      }
    }),
    searchProducts: builder.query({
      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        const { search, type, fetchWithEmptyString = true } = args;
        if (search === '' && !fetchWithEmptyString) {
          return { data: [] };
        }

        let data = [];
        if (type === 'products') {
          const query1 = `/${restBase}?search=${search}&per_page=100&orderby=title&order=asc`;

          data = await fetchWithBQ(query1);

          if (data.error) return { error: data.error };

          data = data.data;
        }
        return { data };
      }

    }),
    getProductsToShow: builder.query({

      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        let data = { data: [] };
        const { searchFor = '', searchForProp = '', fetchWithEmptyTerms = true, termsIds = [], termsType = '', exclusion = { type: 'products', ids: [] }, numberOfProducts = -1, context } = args;

        if (searchFor === 'products') {
          if (exclusion?.ids && exclusion.ids.length > 0) {
            data = await getExcludedProducts(exclusion, fetchWithBQ, context);
          } else {
            data = await getProductsById(termsIds, fetchWithEmptyTerms, fetchWithBQ, context);
          }
          data = data.data;
        } else {
          let dataToProcess = [];
          if (searchFor === 'featured') {
            dataToProcess = await getFeaturedProducts(fetchWithBQ, context);
          } else if (searchFor === 'best-seller') {
            dataToProcess = await getBestSellerProducts(fetchWithBQ, context);
          } else {
            if (termsType === 'categories') {
              dataToProcess = await getProductsByCategories(termsIds, fetchWithBQ, context);
            } else {
              dataToProcess = await getProductsById(termsIds, fetchWithEmptyTerms, fetchWithBQ, context);
            }
          }
          if (dataToProcess.error) return { error: dataToProcess.error };
          dataToProcess = dataToProcess.data;
          if (searchForProp !== '') {
            const property = searchForProp.replace('-', '_') + '_ids';

            dataToProcess = await getProductsByProperty(dataToProcess, property, fetchWithBQ, context);
            dataToProcess = dataToProcess.data;
          }
          data = dataToProcess;
        }
        if (numberOfProducts !== -1 && data.length > 0) {
          const shuffledData = [...data];
          if (termsType.type === 'categories') {
            data = shuffledData.filter(singleProduct => {
              const catsIds = singleProduct.categories.map(obj => obj.id);
              const intersection = termsIds.filter(x => catsIds.includes(x));
              if (intersection.length > 0) return true;
              return false;
            });
          }
          if (data.length > 0 && numberOfProducts !== -1) {
            const randomIndex = Math.floor(Math.random() * data.length);
            data = numberOfProducts === 1 ? data[randomIndex] : data.slice(0, numberOfProducts);
          }
        }
        return { data };
      }
    })
  })
});

export const {
  useGetProductsQuery,
  useSearchProductsQuery,
  useGetProductsToShowQuery
} = yithApiProductSlice;
