import React, { useEffect, useLayoutEffect, useRef, useState } from 'react';

import { Box, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import QuantityDiscountItem from './quantity-discount-item';
import QuantityDiscountItemEmpty from './quantity-discount-item-empty';
import { Divider } from '../../common/fields';
import Icon from '../../common/icon';
import { isEmpty } from '../../common/utils/common';
import { getRandomProductToShow } from '../../common/utils/data';
import { getDiscountedPrice } from '../../common/utils/numbers';
import AddToCartButton from '../elements/add-to-cart-button';
import AddToCartProductListButton from '../elements/add-to-cart-product-list-button';
import Price from '../elements/price';
import SaveDiscount from '../elements/save-discount';
import { VerticalScrollableContentStyled } from '../elements/styles/VerticalScrollableContent.styled';

const QuantityDiscount = ({ campaign, campaignTitle, step = false, preview = false, context = 'frontend', productID = false, addToCartClasses = '' }) => {
  const showContent = campaign.status === 'publish' || step !== 'trigger';
  const verticalScrollRef = useRef();
  const [disableAddToCart, setDisableAddToCart] = useState(false);
  const windowGap = preview ? 450 : 350;
  const [verticalScroll, setVerticalScroll] = useState(window.innerHeight - windowGap);
  const [hasScroll, setHasScroll] = useState(false);
  const checkScroll = () => {
    if (verticalScrollRef.current) {
      const newScroll = (verticalScrollRef.current.scrollHeight > verticalScrollRef.current.clientHeight);
      setHasScroll(newScroll);
    }
  };
  const handleResize = () => {
    setVerticalScroll(window.innerHeight - windowGap);
    checkScroll();
  };

  const options = campaign.meta[campaign.type];
  const {
    show_total_amount: showTotals,
    discount_rules: discountRules,
    trigger_product: triggerProduct,
    show_saving: showSaving
  } = options;

  const rules = isEmpty(discountRules)
    ? [
        { id: 0, unit: 5, discount: 10, discountType: 'percentage', checked: true },
        { id: 1, unit: 10, discount: 30, discountType: 'percentage', checked: false }]
    : [...discountRules];

  const [selectedRule, setSelectedRule] = useState(rules.find(rule => rule.checked === true));
  const [currentList, setCurrentList] = useState([]);
  const [responsiveClass, setResponsiveClass] = useState('');
  const productToShow = preview ? triggerProduct : { type: 'products', ids: [productID] };
  const { data: product, isFetching } = getRandomProductToShow(productToShow);
  const [errors, setErrors] = useState({ quantity: false, notFoundVariation: false });
  const canAddToCart = () => {
    if (!product) {
      return false;
    }
    if (product.type !== 'variable') return true;

    if (isEmpty(currentList)) return false;
    let enableAddToCart = true;
    let totalQty = 0;

    // checking founded variations and calculating the total amount.
    currentList.forEach(innerVariation => {
      if (parseInt(innerVariation.quantity) > 0) {
        totalQty += parseFloat(innerVariation.quantity);
        if (!innerVariation.selectedVariation || !(innerVariation.selectedVariation.variation_is_visible && innerVariation.selectedVariation.is_in_stock && innerVariation.selectedVariation.is_purchasable)) {
          enableAddToCart = false;
        }
      }
    });
    const notFoundVariation = !enableAddToCart;
    // comparing the total amount with the rule quantity.
    if (parseInt(selectedRule.unit) !== totalQty) {
      setErrors({ notFoundVariation, quantity: true });
      enableAddToCart = false;
    } else {
      setErrors({ notFoundVariation, quantity: false });
    }

    return enableAddToCart;
  };

  const ref = useRef();
  useLayoutEffect(() => {
    const width = ref.current.offsetWidth;

    if (width > 450 && width <= 530) {
      setResponsiveClass('responsive-400');
    }
    if (width <= 450) {
      setResponsiveClass('responsive-mobile');
    }
  }, []);

  useEffect(() => {
    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  useEffect(() => {
    checkScroll();

    if (preview) {
      const currentSelected = rules.find(rule => rule.checked === true);
      if (selectedRule?.id !== currentSelected.id) {
        setSelectedRule(rules.find(rule => rule.checked === true));
      }
    }
  }, [rules]);

  useEffect(() => {
    setDisableAddToCart(!canAddToCart());
  }, [product, currentList, selectedRule]);
  const getDiscountedTotal = () => {
    const checkedRule = rules.find(rule => rule.id === selectedRule.id);
    let discountTotal = 0;
    let discountValue = 0;
    let discountedPrice = 0;
    if (product.type === 'variable' && !isEmpty(currentList)) {
      discountTotal = currentList.reduce((accumulator, variation) => {
        if (variation?.selectedVariation) {
          discountValue = checkedRule.discountType === 'fixed' ? parseFloat(checkedRule.discount) / parseInt(variation.quantity) : parseFloat(checkedRule.discount);
          discountedPrice = getDiscountedPrice(variation.selectedVariation.regular_price, discountValue, checkedRule.discountType);
          return accumulator + (parseInt(variation.quantity) * discountedPrice);
        }

        return accumulator;
      }, 0);
    } else {
      discountValue = checkedRule.discountType === 'fixed' ? parseFloat(checkedRule.discount) / parseInt(checkedRule.unit) : parseFloat(checkedRule.discount);
      discountedPrice = getDiscountedPrice(product.regular_price, discountValue, checkedRule.discountType);
      discountTotal = discountedPrice * checkedRule.unit;
    }
    return discountTotal;
  };
  const getTotal = () => {
    let total = 0;
    let checkedRule = rules.find(rule => rule.id === selectedRule.id);
    if (!checkedRule) {
      checkedRule = rules[0];
    }
    if (product.type === 'variable' && !isEmpty(currentList)) {
      total = currentList.reduce((accumulator, variation) => {
        return accumulator + (variation.selectedVariation ? (parseInt(variation.quantity) * parseFloat(variation.selectedVariation.regular_price)) : 0);
      }, 0);
    } else {
      total = product.regular_price * checkedRule.unit;
    }
    return total;
  };

  const handleCheckboxChange = (rule, list) => {
    setSelectedRule(rule);
    if (product.type === 'variable') {
      setCurrentList(list);
    }
  };

  const handleUpdateList = (list) => {
    setCurrentList(list);
  };

  const handleAddToCart = (e) => {
    e.preventDefault();

    const formData = {
      'yith-sales-add-to-cart': productID,
      campaign_id: campaign.id,
      rule: selectedRule,
      list: currentList
    };
    fetch(
      product.permalink, {
        method: 'POST',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      }).then((response) => {
      if (response.status !== 200) {
        throw new Error(response.statusText);
      } else {
        window.location.replace(response.url);
      }
      return response.json();
    }).then((data) => {
      if (data?.redirect_to) {
        window.location.replace(data.redirect_to);
      }
    }).catch((err) => {
      console.log(err.toString());
    });
  };
  const saved = (!isFetching && product) ? getTotal() - getDiscountedTotal() : 0;

  return (

      <Box className={`yith-sales-quantity-discount-preview-wrapper ${responsiveClass}`} ref={ref} sx={{ minWidth: '400px', maxWidth: '650px', paddingRight: '30px' }}>
        {!showContent && <QuantityDiscountEmpty/>}
        {showContent &&
            <form
                action={product && product.permalink}
                method="POST"
            >
              {campaignTitle}
              {(isFetching || !product) && <Box sx={{ textAlign: 'center' }}><Spinner/></Box>}
              <VerticalScrollableContentStyled ref={verticalScrollRef} className={`scrollable ${hasScroll ? 'with-scroll' : ''}`} maxHeight={`${verticalScroll}px`} paddingRight="20px" marginTop="20px">
                {!isFetching && product && rules.map(rule => <QuantityDiscountItem context={context} errors={errors} key={rule.id} product={product} rule={rule} options={options} checked={rule.id === selectedRule.id} onClickCheckbox={handleCheckboxChange} onUpdateList={handleUpdateList}/>)}
              </VerticalScrollableContentStyled>
              {product && showTotals === 'yes' &&
                  <Box className="yith-sales-products-total" sx={{ marginTop: '23px', marginBottom: '17px' }}>
                    <Stack direction="row" sx={{gap: '5px'}} justify="center" align="center">
                      <Typography sx={{ fontWeight: '700', lineHeight: '22px', fontSize: '1.2em' }}>{__('Total:', 'wonder-cart')}</Typography>
                      <Price price={getDiscountedTotal()}/>
                      {saved > 0 && <Price price={getTotal()} showDel={true}/>}
                      {showSaving === 'yes' && saved > 0 &&
                          <SaveDiscount saved={saved} discountType="fixed" usePrecision={true}/>
                      }
                    </Stack>
                  </Box>
              }
              <Box sx={{ zIndex: '10', textAlign: 'center', marginRight: '15px', width: '100%' }}>
                {!preview && product && product.type === 'variable' && (errors.quantity !== false || errors.notFoundVariation !== false) &&
                    <Box className="error-box-wrapper" sx={{ backgroundColor: '#FFEED9', color: '#CC3606', textAlign: 'center', fontSize: '0.9rem', padding: '7px', marginBottom: '10px' }}>
                      <Stack direction="row" justify="center" align="center" className="error-box">
                        <Icon icon="ExclamationTriangle" size={18} sx={{ color: '#CC3606', margin: '3px 10px 1px', display: 'inline-block', lineHeight: '0.9rem' }}/>
                        <Box as="span">{__('Please set the options of missing quantities before to add to cart', 'wonder-cart')}</Box>
                      </Stack>
                    </Box>
                }
                <AddToCartProductListButton label={__('Add to cart', 'wonder-cart')} className={addToCartClasses} isDisabled={disableAddToCart} context={context} onClick={handleAddToCart}/>
              </Box>
            </form>
        }
      </Box>

  );
};

const QuantityDiscountEmpty = () => {
  return (
      <>
        <QuantityDiscountItemEmpty key="empty-1" ruleQty="5" checked={true}/>
        <QuantityDiscountItemEmpty key="empty-2" ruleQty="10" checked={false}/>
        <Box className="yith-sales-products-total" sx={{ marginTop: '23px', marginBottom: '17px' }}>
          <Divider sx={{ width: '100%', marginBottom: '8px', marginTop: '20px', borderBottomWidth: 'thick' }}/>
          <Divider sx={{ width: '100%', marginBottom: '30px', borderBottomWidth: 'thick' }}/>
        </Box>
        <Box sx={{ zIndex: '10', textAlign: 'center', marginRight: '15px', width: '100%' }}>
          <AddToCartButton label={__('Add to cart', 'wonder-cart')}/>
        </Box>
      </>
  );
};

export default QuantityDiscount;
