import React from 'react';

import { Box } from '@lapilli-ui/components';

import { DiscountWrapper, QuantityDiscountItemContent, QuantityDiscountItemWrapper } from './styled';
import { Divider } from '../../common/fields';
import Checkbox from '../../common/fields/checkbox';
import { getImage } from '../../common/utils/common';
import ProductImage from '../elements/product-image';

const QuantityDiscountItemEmpty = ({ ruleQty, checked }) => {
  const productImage = getImage({}, true);

  return (
      <QuantityDiscountItemWrapper>
        <QuantityDiscountItemContent >
        <Box>
          <Checkbox context={'frontend'} checked={checked} readOnly/>
        </Box>
          <ProductImage src={productImage.src} alt={productImage.alt} width="55px" badge={ruleQty} badgeBorderRadius='10px'/>
          <Divider sx={{ width: '120px', marginBottom: '30px', borderBottomWidth: 'thick' }}/>
            <DiscountWrapper className={ checked ? 'discount-selected' : 'discount'} minWidth="181" flex="1">
            </DiscountWrapper>

        </QuantityDiscountItemContent>

      </QuantityDiscountItemWrapper>

  );
};

export default QuantityDiscountItemEmpty;
