import React from 'react';

import { Box, Stack } from '@lapilli-ui/components';

import { isEmpty } from '../../../common/utils/common';
import SimpleButton from '../simple-button';

const CategoryPromotion = ({ data, content, buttonLabel }) => {
  if (isEmpty(data)) {
    return '';
  }

  const category = data[0];
  return (
        <Stack direction='column' align='center' className="yith-sales-x-y-modal-campaign">
            <Box>{content}</Box>
            <SimpleButton href={category?.permalink} label={buttonLabel} sx={{
              backgroundColor: 'var(--wp--preset--color--primary)',
              borderRadius: 'var(--wp--custom--button-border-radius)',
              borderWidth: 0,
              borderStyle: 'solid',
              color: 'var(--wp--preset--color--base)!important',
              fontSize: 'var(--wp--preset--font-size--small)',
              fontWeight: 400,
              paddingTop: '0.5rem',
              paddingRight: '1rem',
              paddingBottom: '0.5rem',
              paddingLeft: '1rem',
              marginBottom: '10px',
              marginTop: '20px'
            }
            }
            />
        </Stack>
  );
};

export default CategoryPromotion;
