import React from 'react';

import ModalCampaign from './modal-campaign';
import { useModalSwitch } from '../../../common/utils/common';

const ModalCampaignsWrap = ({ campaigns, reloadCheckout }) => {
  const [currentModalOpened, { handleCloseModal }] = useModalSwitch(campaigns, 'yith_sales_modal_campaigns', 1);

  return (
        <>
            { campaigns.map(campaign => {
              return <ModalCampaign open = {currentModalOpened.id === campaign.id} key={ campaign.id} campaign={campaign} handleOnClose={handleCloseModal} reloadCheckout={reloadCheckout}/>;
            })
            }
        </>
  );
};

export default ModalCampaignsWrap;
