import React from 'react';

import { createTheme, ThemeProvider } from '@lapilli-ui/styles';
import { render } from '@wordpress/element';

import FreeShippingModule from './free-shipping-module';
import { $ } from '../../../common/config';
import { buildInitialSettingState } from '../../../common/utils/common';
import { getGlobalTheme, mainTheme } from '../../theme';

const initFreeShippingBanner = () => {
  const freeShippingBanners = document.querySelectorAll('.yith-sales-cart-banner:not(.yith-sales-cart-banner-initialized)');
  let index = 0;
  const settings = buildInitialSettingState();
  const globalThemeSettings = getGlobalTheme(settings);
  const globalTheme = createTheme({ ...mainTheme, ...globalThemeSettings });
  for (const freeShippingBanner of freeShippingBanners) {
    const options = JSON.parse(freeShippingBanner.dataset.options);
    freeShippingBanner.classList.add('yith-sales-cart-banner-initialized');

    render(
            <React.StrictMode>
                <ThemeProvider theme={globalTheme}>
                    <FreeShippingModule key={index++} {...options} />
                </ThemeProvider>
            </React.StrictMode>,
            freeShippingBanner
    );
  }
};
$(document.body).on('updated_wc_div', initFreeShippingBanner);
initFreeShippingBanner();
