import React, { useEffect, useState } from 'react';

import { addQueryArgs } from '@wordpress/url';
import parse from 'html-react-parser';

import ThankYouPage from '../../../blocks/thank-you-page';
import { useGetCampaignQuery } from '../../../common/api/campaign';
import { $, ajaxAction, ajaxNonce, ajaxURL, checkoutURL } from '../../../common/config';
import { getCampaignTitle } from '../../../common/utils/common';

const ThankYouPageModule = ({ campaignID }) => {
  const { data, isFetching } = useGetCampaignQuery(campaignID);
  const [addedToCart, setAddedToCart] = useState(false);

  const handleSubmitVariation = (e, handleData) => {
    e.preventDefault();
    const formData = {
      campaign_id: campaignID,
      products: {
        ...handleData
      }
    };
    const queryArgs = addQueryArgs(ajaxURL, {
      action: ajaxAction,
      security: ajaxNonce,
      yith_sales_action: 'add_thank_you_page_upsell',
      yith_sales_data: formData
    });
    fetch(queryArgs).then((response) => {
      if (response.status !== 200) {
        throw new Error(response.statusText);
      }
      return response.json();
    }).then((data) => {
      if (data?.fragments) {
        setAddedToCart(true);
      } else {
        window.location.replace(data.redirect_to);
      }
    }).catch((err) => {
      console.log(err.toString());
    });
  };
  useEffect(() => {
    if (data) {
      const handleAddingButton = (e, button, dataEvent) => {
        if (data.id === parseInt(dataEvent.campaign_id)) {
          const meta = data.meta[data.type];
          if (meta.redirect_to_checkout === 'yes') {
            wc_add_to_cart_params.cart_redirect_after_add = 'yes';
            wc_add_to_cart_params.cart_url = checkoutURL;
          }
        }
      };

      $(document.body).on('adding_to_cart', handleAddingButton);

      return () => $(document.body).off('adding_to_cart', handleAddingButton);
    }
  }, [data]);

  if (isFetching) return '';
  const campaignTitle = getCampaignTitle(data);
  const campaignTitleField = parse(campaignTitle);
  return (
      <>{!isFetching && data && <ThankYouPage campaign={data} campaignTitle={campaignTitleField} preview={false} context="frontend" onSubmitVariation={handleSubmitVariation} showViewCartButton={addedToCart} onCloseModal={() => setAddedToCart(false)}/>}</>
  );
};
export default ThankYouPageModule;
