import React from 'react';

import { Box } from '@lapilli-ui/components';

import ProductSingleItem from '../product-single-item';
import SimpleModal from '../simple-modal';

const ProductInModal = ({ product, opened, onCloseModal, onSubmit, productListOPT, showViewCartButton }) => {
  return (
      <>
        <SimpleModal
            open={opened}
            maxWidth='336px'
            onClose={onCloseModal}
            isFullWidth={false}
        >
          <Box className="woocommerce" sx={{ width: 'fit-content', margin: '0 auto' }}>
            <ProductSingleItem data={[product]} productOpts={productListOPT} handleNoThanks={onCloseModal} showViewCartButton={showViewCartButton} context='frontend' addToCartClassName={product.addToCartClasses} onSubmit={onSubmit}/>
          </Box>
        </SimpleModal>
      </>
  );
};

export default ProductInModal;
