import React from 'react';

import { Box, Spinner, Stack, Typography } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';

import { Divider } from '../../common/fields';
import { getImage, getNoProductLabel, isEmpty } from '../../common/utils/common';
import { getProductsToShow } from '../../common/utils/data';
import BoxWithInfo from '../elements/box-with-info';
import HorizontalProductSlider from '../elements/horizontal-product-slider';
import ProductImage from '../elements/product-image';

const ThankYouPage = ({ campaign, campaignTitle, step = false, preview = false, context = 'admin', onSubmitVariation, showViewCartButton = false, onCloseModal }) => {
  const showContent = campaign.status === 'publish' || step !== 'trigger';
  const options = campaign.meta[campaign.type];
  let {
    show_saving: showSaving,
    trigger_product: triggerProduct,
    products_to_show: productsToShow,
    show_badge: showBadge,
    badge_text: badgeText,
    badge_background_color: badgeBackgroundColor,
    badge_text_color: badgeColor,
    discount_to_apply: discountToApply,
    apply_discount: applyDiscount,
    show_product_price: showProductPrice,
    num_products_to_show: numberOfProducts,
    show_product_name: showProductName,
    show_add_to_cart: showAddToCart,
    add_to_cart_button_label: addToCartLabel
  } = options;

  numberOfProducts = (productsToShow.type === 'products') ? -1 : numberOfProducts;

  const { data, isLoading, isFetching } = getProductsToShow(productsToShow, triggerProduct);
  const inFetching = isFetching || isLoading;
  const { discount_value: discountValue, discount_type: discountType } = discountToApply;
  if (preview && step !== 'customization') {
    showBadge = 'no';
  }

  const getEmptyObject = (key) => {
    const generalImage = getImage({}, true);
    return (<Stack key={key}>
      <ProductImage src={generalImage.src} alt={generalImage.alt} width='200px'/>
      <Divider sx={{ width: '100%', margin: '20px auto 10px', borderBottomWidth: 'thick' }}/>
      <Divider sx={{ width: '100%', marginBottom: '10px', borderBottomWidth: 'thick' }}/>
      <Divider sx={{ width: '100%', borderBottomWidth: 'thick' }}/>
    </Stack>);
  };
  const maxWidth = preview ? '730px' : '100%';
  const campaignProps = { id: campaign.id, showBadge, badgeText, badgeBackgroundColor, badgeColor, showProductName, applyDiscount, discountValue, discountType, showProductPrice, showSaving, showAddToCart, addToCartLabel };

  if (preview && !inFetching && isEmpty(data)) {
    const noResultLabel = getNoProductLabel(productsToShow.type);
    return (
        <BoxWithInfo info={noResultLabel} justify='center' iconSize={20} icon='InformationCircleIcon'
                     bgColor='#fff' color='#000' borderColor="#cacaca" margin='0 auto' minWidth={'500px'}/>
    );
  }

  const sxBox = {
    backgroundColor: '#fff', padding: '40px 60px'
  };
  return (
      <Box className="yith-sales-thank-you-page-preview-wrapper" sx={ preview ? sxBox : {}}>
        { preview && <>
        <Typography sx={{ marginTop: '20px', marginBottom: '50px', color: '#E4E4E4', fontSize: '40px', textTransform: 'uppercase', lineHeight: '17px', textAlign: 'center', fontWeight: '700', letterSpacing: '3px' }}> {__('Thank you!', 'wonder-cart')}</Typography>
        <Divider sx={{ width: '50%', margin: '0 auto 15px', borderBottomWidth: 'thick' }}/>
        <Divider sx={{ width: '70%', margin: '0 auto 15px', borderBottomWidth: 'thick' }}/>
        <Divider sx={{ width: '70%', margin: '0 auto 60px', borderBottomWidth: 'thick' }}/>
        </> }
        { (step === 'customization' || context === 'frontend') && (
            <>{campaignTitle}</>
        )
        }
        {showContent && <>
          { inFetching && <Box sx={{ textAlign: 'center', width: '100%' }}><Spinner/></Box>}
          {!inFetching && data && data.length > 0 &&
              <HorizontalProductSlider maxWidth={maxWidth} campaignProps={campaignProps} products={data} numberOfProducts={numberOfProducts} preview={preview} context={context} handleAddToCartInModal={onSubmitVariation} showViewCartButton={showViewCartButton} onCloseModal={onCloseModal}/>
          }
        </>}
          {preview && (!showContent || (!inFetching && !data)) && <Stack direction="row"sx={{ paddingBottom: '50px', gap: '36px' }}>{[...Array(3)].map((x, i) => getEmptyObject(i))}</Stack> }
      </Box>
  );
};

export default ThankYouPage;
