import React from 'react';

import { Input, Stack, Typography } from '@lapilli-ui/components';

import { QuantityDiscountVariations } from './styled';
import Icon from '../../common/icon';
import ProductVariations from '../elements/product-variations';

const QuantityDiscountVariation = ({ product, variation, rule, onFoundVariation, onSelectAttributes, onChangeQuantity, available, calculateQty, errors, checked }) => {
  const handleOnSelectAttributes = (product, selectedAttributes) => {
    onSelectAttributes(variation.id, selectedAttributes);
  };
  const handleOnFoundVariation = (product, variationSelected) => {
    onFoundVariation(variation.id, variationSelected);
  };

  let maxValue = parseInt(variation.quantity) + available;

  if (variation?.selectedVariation?.stock_quantity > 0) {
    const tot = calculateQty(variation.selectedVariation) - variation.quantity;
    const rest = variation.selectedVariation.stock_quantity - tot;
    maxValue = maxValue > rest ? rest : maxValue;
  }
  const variationClass = checked && !variation.selectedVariation ? 'not-found-variation' : '';
  return (
        <>
        <QuantityDiscountVariations className={`variation-info ${variationClass}`}>
          <Stack>
            <Input type="number" value={variation.quantity} min="0" step="1" max={maxValue} size="sm" isMini={true} onChange={(e) => onChangeQuantity(e, variation)}/>
          </Stack>
          <Stack sx={{ flex: 1 }}>
            <Stack direction='row'>
          <ProductVariations product={product} defaultAttributes={variation.selectedAttributes} onFoundVariation={handleOnFoundVariation} onSelectAttributes={handleOnSelectAttributes}/>
          <Stack className={`variation-error ${variationClass}`}>{<Icon icon="ExclamationTriangle" size={18} sx={{ color: '#CC3606', margin: '0 5px' }}/>}</Stack>
            </Stack>
            {variation.selectedVariation && <Typography className={`stock ${variation.selectedVariation.availability.class}`} style={{ marginLeft: '7px' }}>{variation.selectedVariation.availability.availability}</Typography>}
          </Stack>

        </QuantityDiscountVariations>

        </>

  );
};

export default QuantityDiscountVariation;
