import { useEffect, useMemo, useState } from 'react';

import { useTheme } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';
import { useCookies } from 'react-cookie';

import { imgPlaceholder, panelSettings, productToShowOptions } from '../config';

export const getImage = (item, full = false) => {
  const image = {
    src: item?.images?.[0]?.[full ? 'src' : 'thumbnail'] ?? imgPlaceholder,
    alt: item?.images?.[0]?.alt ?? ''
  };

  return image;
};

export const getFormattedTermName = (item) => {
  let itemName = item.name;
  if (item?.variation && item.variation !== '') {
    itemName += ' ' + item.variation;
  }
  return htmlEntityDecode(itemName);
};

export const htmlEntityDecode = (string) => {
  const parser = new DOMParser();
  return parser.parseFromString(`<!doctype html><body>${string}`, 'text/html').body.textContent;
};

/**
 * Check if an element object or array is empty
 *
 * @param element
 * @returns {boolean}
 */
export const isEmpty = (element) => {
  if (typeof element === 'undefined') {
    return true;
  }

  if (typeof element === 'object') {
    return Object.keys(element).length === 0;
  }

  return element.hasOwnProperty('length') ? element.length === 0 : 'undefined';
};

export const unserializeMeta = (meta) => {
  const newMeta = {};
  for (const key in meta) {
    newMeta[key] = isJson(meta[key]) ? JSON.parse(meta[key]) : meta[key];
  }
  return newMeta;
};

const isJson = (text) => {
  try {
    JSON.parse(text);
    return true;
  } catch (error) {
    return false;
  }
};

export const serializeMeta = (meta) => {
  const newMeta = {};
  for (const k in meta) {
    if (typeof meta[k] !== 'string') {
      newMeta[k] = JSON.stringify(meta[k]);
    } else {
      newMeta[k] = meta[k];
    }
  }
  return newMeta;
};

export const buildInitialSettingState = () => {
  const settings = {};

  for (const panelId in panelSettings) {
    const sections = panelSettings[panelId].sections;
    settings[panelId] = {};
    if (sections) {
      for (const sectionId in sections) {
        const options = sections[sectionId].options;
        settings[panelId][sectionId] = {};
        for (const optionKey in options) {
          const option = options[optionKey];
          settings[panelId][sectionId][option.id] = option.value;
        }
      }
    }
  }
  return settings;
};

export const shuffle = (myArray) => {
  return myArray.sort(() => Math.random() - 0.5);
};

export const getOptionFromTaxTerms = (terms) => {
  const options = [{ label: __('Choose option', 'wonder-cart'), value: 'any' }];
  terms.map(term => options.push({ label: term.name, value: term.slug }));
  return options;
};

export const isFieldValid = (currentField, fields) => {
  const isActive = isFieldActive(currentField, fields);
  if (!isActive) return true; // the field is not visible and shouldn't be considered.
  const value = currentField?.value;

  switch (currentField.type) {
    case 'selectTerms':
      return !((value?.type === 'products' || value?.type === 'categories') && isEmpty(value?.ids));
    case 'cartDiscountRules':
    case 'giftDiscountRules':
    case 'freeShippingRules':
      return !(value?.type === 'conditions' && isEmpty(value?.rules));
    default:
      return !isEmpty(value);
  }
};

export const isFieldActive = (currentField, fields) => {
  let isActive = true;
  if (currentField.required && currentField?.deps) {
    if (currentField?.deps?.id) {
      const fieldDep = findFieldById(currentField.deps.id, fields);
      if (fieldDep && fieldDep.value !== currentField.deps.value) {
        isActive = false;
      }
    } else if (currentField?.deps.length > 0) {
      currentField?.deps.forEach(dep => {
        const fieldDep = findFieldById(dep.id, fields);
        if (fieldDep && fieldDep.value !== dep.value) {
          isActive = false;
          return true;
        }
      });
    }
  }

  return isActive;
};

export const findFieldById = (id, fields) => {
  let foundedField = false;
  fields.forEach(field => {
    if (foundedField) return;
    if (field.type === 'toggle') {
      foundedField = field.fields.find(subfield => subfield.id === id);
    } else if (field.id === id) {
      foundedField = field;
    }
  });

  return foundedField;
};

export const getNoProductLabel = type => {
  const option = productToShowOptions.find(opt => opt.value === type);
  return option.noProductLabel ? option.noProductLabel : __('No products found.', 'wonder-cart');
};

export const getCampaignTitle = campaign => {
  const options = campaign.meta[campaign.type];

  return options?.title;
};
/**
 * This use function, manage the modal logic, when more campaigns should be shown in modal
 *
 * @param campaigns
 * @param cookieName
 * @param expireInDays
 * @param canAutoHide
 * @returns {[string,{handleCloseModal: handleCloseModal}]}
 */
export const useModalSwitch = (campaigns, cookieName, expireInDays = 30, canAutoHide = false, initialDelay = false) => {
  const [currentModalOpened, setCurrentModalOpened] = useState('');
  const [userClosing, setUserClosing] = useState(false);
  const [nextCampaign, setNextCampaign] = useState(0);
  const [cookies, setCookie] = useCookies([cookieName]);
  const theme = useTheme();

  const addCampaignInCookie = (campaign) => {
    const hash = campaign.id + ':' + campaign.modified;
    let modalsHided = cookies[cookieName];

    if (modalsHided === undefined) {
      modalsHided = [hash];
    } else {
      if (modalsHided.indexOf(hash) === -1) {
        modalsHided.push(hash);
      }
    }
    const today = new Date();
    const expireDate = new Date(new Date().setDate(today.getDate() + expireInDays));
    setCookie(cookieName, modalsHided, { path: '/', expires: expireDate });
  };
  const handleCloseModal = () => {
    addCampaignInCookie(currentModalOpened);
    setCurrentModalOpened('');
    setUserClosing(true);
    if (nextCampaign + 1 < campaigns.length) {
      setTimeout(() => {
        setNextCampaign(nextCampaign + 1);
      }, theme.notifications.waitAfterClose * 1000);
    }
  };

  useEffect(() => {
    const campaign = campaigns[nextCampaign];
    const delay = initialDelay ?? theme.notifications.initialDelay;
    const timer = setTimeout(() => {
      setCurrentModalOpened(campaign);
      addCampaignInCookie(campaign);
    }, delay * 1000);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    if (canAutoHide && theme.notifications.hideNotification === 'yes' && !userClosing) {
      let timer = '';
      if (currentModalOpened !== '') {
        timer = setTimeout(() => {
          setCurrentModalOpened('');
        }, theme.notifications.hideNotificationAfter * 1000);
      } else {
        if (nextCampaign + 1 < campaigns.length) {
          timer = setTimeout(() => {
            setNextCampaign(nextCampaign + 1);
          }, theme.notifications.timeBetween * 1000);

          return () => clearTimeout(timer);
        }
      }
    }
  }, [currentModalOpened]);

  useEffect(() => {
    if (nextCampaign > 0 && nextCampaign < campaigns.length) {
      const campaign = campaigns[nextCampaign];
      setCurrentModalOpened(campaign);
      addCampaignInCookie(campaign);
      setUserClosing(false);
    }
  }, [nextCampaign]);

  return [currentModalOpened, { handleCloseModal }];
};

export const getPopupOptions = (options, type = 'popup') => {
  const isFullWidth = type === 'popup' ? options.show_popup_full_size === 'yes' : options.show_promotion_full_size === 'yes';
  const iconColor = type === 'popup' ? options.close_icon_color : options.color_promotion_popup_close_icon;
  const bgColor = type === 'popup' ? options.full_size_background_color : options.background_promotion_popup_color;
  let borderRadiusDimension;
  let position;
  if (!isFullWidth) {
    borderRadiusDimension = type === 'popup' ? options.popup_border_radius_item.dimension : options.promotion_border_radius_item.dimension;
    position = type === 'popup' ? options.show_popup_position : options.show_promotion_position;
  }
  const borderRadius = !isFullWidth ? Object.values(borderRadiusDimension).join('px ') + 'px' : undefined;
  const animation = type === 'popup' ? options.popup_animation : options.promotion_animation;

  return { isFullWidth, iconColor, bgColor, borderRadius, animation, position };
};

export const useMediaQuery = (query) => {
  const mediaQuery = useMemo(() => window.matchMedia(query), [query]);
  const [match, setMatch] = useState(mediaQuery.matches);

  useEffect(() => {
    const onChange = () => setMatch(mediaQuery.matches);
    mediaQuery.addEventListener('change', onChange);

    return () => mediaQuery.removeEventListener('change', onChange);
  }, [mediaQuery]);

  return match;
};
