import React, { useEffect } from 'react';

import { Box } from '@lapilli-ui/components';
import parse from 'html-react-parser';

import { $ } from '../../../common/config';

const Badge = ({ content, badgeNode, onSaleNode, galleryNode }) => {
  let badgeObj = {
    x: badgeNode.offsetLeft,
    y: badgeNode.offsetTop,
    w: badgeNode.offsetWidth,
    h: badgeNode.offsetHeight
  };
  const onSaleObj = onSaleNode
    ? {
        x: onSaleNode.offsetLeft,
        y: onSaleNode.offsetTop,
        w: onSaleNode.offsetWidth,
        h: onSaleNode.offsetHeight
      }
    : false;

  const collide = (rect1, rect2) => {
    return rect1.x < rect2.x + rect2.w &&
            rect1.x + rect1.w > rect2.x &&
            rect1.y < rect2.y + rect2.h &&
            rect1.h + rect1.y > rect2.y;
  };
  const mutationManager = (mutationRecords) => {
    mutationRecords.forEach(mutation => {
      if (mutation.attributeName === 'style') {
        const height = mutation.target.style.height.replace('px', '');
        badgeNode.style.top = (height - badgeObj.h - 15) + 'px';
        badgeNode.style.bottom = 'auto';
      }
    });
  };
  const observer = new MutationObserver(mutationManager);
  const initGalleryNode = () => {
    const zoomIcon = galleryNode.querySelector('.woocommerce-product-gallery__trigger');
    if (zoomIcon) {
      const zoomIconObj = {
        x: zoomIcon.offsetLeft,
        y: zoomIcon.offsetTop,
        w: zoomIcon.offsetWidth,
        h: zoomIcon.offsetHeight
      };
      if (collide(zoomIconObj, badgeObj)) {
        badgeNode.style.marginTop = zoomIconObj.y + zoomIconObj.h + 'px';
      }
    }
    if (badgeNode.classList.contains('bottom_left') || badgeNode.classList.contains('bottom_right')) {
      const flexControlNav = galleryNode.querySelector('.flex-viewport');
      if (flexControlNav) {
        observer.observe(flexControlNav, { attributes: true, attributeFilter: ['style'] });
      }
    }
  };
  useEffect(() => {
    if (onSaleObj) {
      if (collide(onSaleObj, badgeObj)) {
        badgeNode.style.marginTop = onSaleObj.y + onSaleObj.h + 'px';
      }
    }
    badgeObj = {
      x: badgeNode.offsetLeft,
      y: badgeNode.offsetTop,
      w: badgeNode.offsetWidth,
      h: badgeNode.offsetHeight
    };
    if (galleryNode) {
      // inside the single product page after that the product gallery render is completed adjust badge position if collide with the zoom icon.

      $(galleryNode).on('wc-product-gallery-after-init', function () {
        initGalleryNode();
      });
      initGalleryNode();

      return () => {
        $(galleryNode).off('wc-product-gallery-after-init');
        observer.disconnect();
      };
    } else {
      // inside the loop if the badge is at the bottom adjust the top position.
      if (badgeNode.classList.contains('bottom_left') || badgeNode.classList.contains('bottom_right')) {
        const product = badgeNode.closest('.product');

        if (product) {
          const image = product.querySelector('img');
          const imageObj = {
            x: image.offsetLeft,
            y: image.offsetTop,
            w: image.offsetWidth,
            h: image.offsetHeight
          };
          badgeNode.style.top = (imageObj.h - badgeObj.h - 15) + 'px';
          badgeNode.style.bottom = 'auto';
        }
      }
    }
  });

  return (
        <Box>
            {parse(content)}
        </Box>
  );
};

export default Badge;
