import React from 'react';

import { Box } from '@lapilli-ui/components';
import { __ } from '@wordpress/i18n';
import parse from 'html-react-parser';

import { addToCartClasses } from '../../common/config';
import Icon from '../../common/icon';
import { getPopupOptions } from '../../common/utils/common';
import { getProductsToShow } from '../../common/utils/data';
import ProductList from '../elements/product-list';
import ProductSingleItem from '../elements/product-single-item';
import SimpleModal from '../elements/simple-modal';
import { AlertStyled } from '../elements/styles/Alert.styled';

const Upsell = ({ campaign, productID, openModal, showViewCartButton, onCloseModal, onSubmit }) => {
  if (campaign === null || productID === null) return '';

  const options = campaign.meta[campaign.type];
  const {
    title,
    num_products_to_show: numProductToShow,
    products_to_show: productToShow,
    discount_to_apply: discountToApply
  } = options;

  const skipMaxNumber = productToShow.type === 'products';
  const triggerProduct = { type: 'products', ids: [productID] };
  const { data: allProducts, isLoading, isFetching } = getProductsToShow(productToShow, triggerProduct, 'edit');
  if (!allProducts || isLoading || isFetching) return '';
  const productListOPT = {
    applyDiscount: options.apply_discount,
    productType: productToShow.type,
    discountValue: discountToApply.discount_value,
    discountType: discountToApply.discount_type,
    showProductName: options.show_product_name,
    showProductPrice: options.show_product_price,
    showSaving: options.show_saving,
    savingColor: options.saving_color,
    addDiscount: options.apply_discount,
    checkboxColor: options.checkbox_color,
    addToCartLabel: options.add_to_cart_button_label,
    checkedByDefault: 'yes'
  };
  const isSingle = (!allProducts || (skipMaxNumber && allProducts.length < 2) || (!skipMaxNumber && (parseInt(options.num_products_to_show) === 1 || allProducts.length < 2)));
  let modalContent = '';
  let maxWidth = '336px';
  if (isSingle) {
    modalContent = <ProductSingleItem data={allProducts} productOpts={productListOPT} handleNoThanks={onCloseModal} showViewCartButton={showViewCartButton} context='frontend' addToCartClassName={addToCartClasses} onSubmit={onSubmit}/>;
  } else {
    maxWidth = '490px';
    modalContent = <ProductList products={allProducts} productOpts={productListOPT} showTotals={false} showViewCartButton={showViewCartButton} numProductToShow={skipMaxNumber ? -1 : numProductToShow} showNoThanks={true} handleNoThanks={onCloseModal} context='frontend' addToCartClassName={addToCartClasses} onSubmit={onSubmit}/>;
  }
  const modalOptions = getPopupOptions(options);

  return (
      <>
      <SimpleModal
          open={openModal}
          maxWidth={maxWidth}
          onClose={onCloseModal}
          context='frontend'
          {...modalOptions}
      >
        <Box sx={{ width: 'fit-content', margin: '0 auto' }}>
          {
            !showViewCartButton
              ? (
                <Box>{parse(title)}</Box>
                )
              : (
                    <AlertStyled
                        severity='success'
                        iconMapping={{
                          success: <Icon icon="CheckCircleIconSolid" />,
                          error: <Icon icon="ExclamationCircleSolid" />,
                          info: <Icon icon="InformationCircleSolid" />,
                          warning: <Icon icon="ExclamationTriangleSolid" />
                        }}
                        sx={{ marginBottom: '20px' }}
                    >
                      {__('Products added to your cart', 'wonder-cart')}
                    </AlertStyled>
                )
          }
        {modalContent}
        </Box>
      </SimpleModal>
      </>
  );
};

export default Upsell;
