import React from 'react';

import { Stack } from '@lapilli-ui/components';

import Product from '../product';
import HorizontalScrollableContentStyled from '../styles/HorizontalScrollableContent.styled';
const HorizontalProductSlider = ({ maxWidth = '100%', campaignProps, products, numberOfProducts, context = 'admin', preview = false, handleAddToCartInModal, showViewCartButton = false, onCloseModal }) => {
  return (
      <HorizontalScrollableContentStyled maxWidth={maxWidth} >
        <Stack direction="row" spacing={2.5} sx={{ padding: '30px 0'}}>
          {products && products.map((product, index) => {
            if (numberOfProducts === -1 || index < numberOfProducts) {
              return (<Product key={product.id} product={product} campaignProps={campaignProps} preview={preview} context={context} showVariableOnModal={true} onModalAddToCartClick={handleAddToCartInModal} showViewCartButton={showViewCartButton} onCloseModal={onCloseModal}/>);
            } else {
              return '';
            }
          })}
        </Stack>
      </HorizontalScrollableContentStyled>
  );
};

export default HorizontalProductSlider;
