import React from 'react';

import { createTheme, ThemeProvider } from '@lapilli-ui/styles';
import { render } from '@wordpress/element';
import { Provider } from 'react-redux';

import BuyXGetY from './buy-x-get-y-module';
import { buildInitialSettingState } from '../../../common/utils/common';
import { frontendStore } from '../../store';
import { getGlobalTheme, mainTheme } from '../../theme';

document.addEventListener('DOMContentLoaded', function () {
  const buyXYSelector = document.querySelector('.yith-sales-x-y-modal');
  if (buyXYSelector) {
    const options = JSON.parse(buyXYSelector.dataset.options);
    const settings = buildInitialSettingState();

    const globalThemeSettings = getGlobalTheme(settings);
    const globalTheme = createTheme({ ...mainTheme, ...globalThemeSettings });
    render(
        <React.StrictMode>
          <Provider store={frontendStore}>
            <ThemeProvider theme={globalTheme}>
              <BuyXGetY {...options}/>
            </ThemeProvider>
          </Provider>
        </React.StrictMode>,
        buyXYSelector
    );
  }
});
