import React from 'react';

import { Stack } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import parse from 'html-react-parser';

const FreeShippingBannerWrapper = styled(Stack, { shouldForwardProp: (prop) => prop !== 'bgColor' })(({
  theme,
  bgColor
}) => ({
  '&': {
    height: '66px',
    lineHeight: '23px',
    width: '100%',
    backgroundColor: bgColor || '#F9F6DA',
    clipPath: 'polygon(100% 0, 95% 50%, 100% 100%, 0% 100%, 5% 50%, 0% 0%)',
    marginBottom: '20px'
  },
  '& .yith-sales-free-shipping-icon': {
    width: '40px',
    height: '24px',
    backgroundImage: 'url(' + theme.freeShipping.freeShippingIcon + ')',
    backgroundSize: 'contain'
  },
  '& .yith-sales-free-shipping-text *': {
    padding: 0,
    margin: 0
  }
}));
const FreeShippingBanner = ({ text, bgColor }) => {
  return (
        <FreeShippingBannerWrapper
            bgColor={bgColor}
            className='yith-sales-free-shipping-banner-wrapper'
            direction='row' align='center' spacing={1.5} justify='center'>
            <div className='yith-sales-free-shipping-icon'></div>
            <div className='yith-sales-free-shipping-text'>
                {parse(text)}
            </div>
        </FreeShippingBannerWrapper>
  );
};

export default FreeShippingBanner;
