import React, { useState } from 'react';

import { Modal, ModalContent, ModalTitle } from '@lapilli-ui/components';
import { keyframes, styled } from '@lapilli-ui/styles';

const zoomIn = keyframes`
  0% {
    transform: scale(0, 0);
  }
  50% {
    transform: scale(.5, .5);
  }
  100% {
    transform: scale(1, 1);
  }
`;

const fadeOut = keyframes`
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
`;
const SimpleModalWrapper = styled(Modal, { shouldForwardProp: (prop) => prop !== 'bgColor' && prop !== 'iconColor' && prop !== 'borderRadius' && prop !== 'overlayColor' && prop !== 'bgImageConfig' })(({
  theme,
  bgColor = theme.modal.backgroundColor,
  iconColor = theme.modal.closeIconColor,
  borderRadius = theme.modal.borderRadius,
  bgImageConfig = {}
}) => ({
  '&:not(.is-scrollable) div[class*="lapilliUI-ModalContent__root"]': {
    overflowY: 'hidden'
  },
  '&.admin h2[class*="ModalTitle__root"]': {
    fontSize: theme.typography.body.fontSize,
    color: theme.palette.primary.main + '!important',
    fontWeight: 700
  },
  '&.frontend': {
    'div[class*="lapilliUI-Backdrop__root"]': {
      background: theme.modal.overlayColor
    },
    'div[class*="lapilliUI-Modal__paper"]': {
      backgroundColor: bgColor + '!important',
      borderRadius: borderRadius || 0,
      ...bgImageConfig
    },
    '&.top_left div[class*="lapilliUI-Modal__paper"]': {
      position: 'absolute',
      top: 0,
      left: 0
    },
    '&.top_right div[class*="lapilliUI-Modal__paper"]': {
      position: 'absolute',
      top: 0,
      right: 0
    },
    '&.bottom_left div[class*="lapilliUI-Modal__paper"]': {
      position: 'absolute',
      bottom: 0,
      left: 0
    },
    '&.bottom_right div[class*="lapilliUI-Modal__paper"]': {
      position: 'absolute',
      bottom: 0,
      right: 0
    },
    '&.fullwidth div[class*="lapilliUI-Modal__paper"]': {
      height: '100%'
    },
    '&.fullwidth div[class*="lapilliUI-ModalContent__root"]': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      fontSize: theme.typography.body2.fontSize
    },
    '&.zoom div[class*="lapilliUI-Modal__paper"]': {
      animation: `${zoomIn} 1s ease forwards!important`
    },
    '.lapilliUI-Modal__paper > button': {
      color: iconColor
    },
    '&.is-closing': {
      animation: `${fadeOut} 1s ease forwards!important`
    }
  },
  '&.disable-portal': {
    position: 'relative',
    width: '100%',
    '&:not(.fullwidth)': {
      background: theme.modal.overlayColor,
      height: 'calc( 100vh - 200px )',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      flexDirection: 'column'
    },
    '&.fullwidth div[class*="lapilliUI-ModalContent__root"]': {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center'
    },
    '&.fullwidth  div[class*="lapilliUI-Modal__paper"]': {
      height: 'calc( 100vh - 200px )'
    },
    'div[class*="lapilliUI-Modal__paper"]': {
      boxShadow: 'unset',
      backgroundColor: bgColor + '!important',
      borderRadius: borderRadius || 0,
      ...bgImageConfig
    },
    'div[class*="lapilliUI-Backdrop__root"]': {
      display: 'none'
    }
  }

}));
const SimpleModal = ({
  open,
  onClose,
  title,
  position,
  isFullWidth,
  maxWidth,
  bgColor,
  iconColor = '',
  disablePortal,
  context = 'admin',
  borderRadius = '',
  bgImageConfig,
  children,
  animation = 'fade',
  scrollable = false
}) => {
  const [isClosing, setIsClosing] = useState(false);
  const width = isFullWidth ? '100%' : maxWidth;
  const positionClass = position === undefined ? '' : position;
  const isClosingClass = isClosing ? 'is-closing' : '';
  const isScrollableClass = scrollable ? 'is-scrollable' : '';
  const handleOnClose = (e, reason) => {
    if (typeof onClose === 'function') {
      setIsClosing(!isClosing);
      setTimeout(() => {
        onClose();
      }, 1000);
    }
  };

  return (
        <SimpleModalWrapper
            className={`yith-sales-modal ${disablePortal ? 'disable-portal' : ''} ${positionClass} ${isClosingClass} ${context} ${isFullWidth ? 'fullwidth' : ''} ${animation} ${isScrollableClass}`}
            open={open} onClose={handleOnClose} fullWidth={isFullWidth} maxWidth={width} bgColor={bgColor}
            iconColor={iconColor} disablePortal={disablePortal} borderRadius={borderRadius}
            bgImageConfig={bgImageConfig} >
            {title && (<ModalTitle>{title}</ModalTitle>)}
            <ModalContent>
                {children}
            </ModalContent>
        </SimpleModalWrapper>
  );
};

export default SimpleModal;
