import React from 'react';

import PromotionModal from './promotion-modal';
import { useModalSwitch } from '../../../common/utils/common';

const PromotionModalWrap = ({ campaigns }) => {
  const [currentModalOpened, { handleCloseModal }] = useModalSwitch(campaigns, 'yith_sales_modal_promotions', 30, true);

  return (
      <>
        { campaigns.map(campaign => {
          return <PromotionModal open = {currentModalOpened.id === campaign.id} key={ campaign.id} campaign={campaign} handleOnClose={handleCloseModal}/>;
        })
        }
      </>
  );
};

export default PromotionModalWrap;
