import { dateI18n } from '@wordpress/date';
import { sprintf } from '@wordpress/i18n';

import { DATE_FORMAT, TIME_FORMAT, wcSettings } from '../config';

export const getDiscountedPrice = (price, discountValue, discountType) => {
  let priceDiscounted = 0;
  if (discountType === 'fixed') {
    priceDiscounted = price - (discountValue);
  } else {
    priceDiscounted = price - (price * discountValue) / 100;
  }

  return priceDiscounted > 0 ? priceDiscounted : 0;
};

/**
 * Formats money with a given currency code. Uses site's currency settings for formatting.
 *
 * @param   {Number|String} number number to format
 * @param   {boolean}        currencySymbol currency code e.g. '$'
 * @param   {boolean}        usePrecision use precision
 *
 * @returns {?String} A formatted string.
 */
export function formatCurrency (number, currencySymbol = false, usePrecision = true) {
  // default to wcSettings (and then to $) if currency symbol is not passed in
  if (!currencySymbol) {
    currencySymbol = wcSettings.currency.symbol;
  }
  const precision = usePrecision ? wcSettings.currency.precision : '';
  // number = precision !== '' ? number / Math.pow(10, parseInt(precision) ): number;

  const formattedNumber = numberFormat(number, precision);

  const priceFormat = wcSettings.currency.price_format;

  if (formattedNumber === '') {
    return formattedNumber;
  }

  return sprintf(priceFormat, currencySymbol, formattedNumber);
}

export function formatTimestamp (timestamp, gmt = true) {
  const format = DATE_FORMAT + ' ' + TIME_FORMAT;
  const date = new Date(timestamp * 1000);

  return dateI18n(format, date, gmt);
}

export function getDatePrefix (prefix) {
  let newPrefix = '';
  for (const term in prefix) {
    newPrefix += '\\' + prefix[term];
  }
  return newPrefix;
}

const numberDecimalFormat = (number, decimals, decPoint, thousandsSep) => {
  number = (number + '').replace(/[^0-9+\-Ee.]/g, '');
  const n = !isFinite(+number) ? 0 : +number;
  const prec = !isFinite(+decimals) ? 0 : Math.abs(decimals);
  const sep = (typeof thousandsSep === 'undefined') ? ',' : thousandsSep;
  const dec = (typeof decPoint === 'undefined') ? '.' : decPoint;
  let s = '';

  const toFixedFix = function (n, prec) {
    if (('' + n).indexOf('e') === -1) {
      return +(Math.round(n + 'e+' + prec) + 'e-' + prec);
    } else {
      const arr = ('' + n).split('e');
      let sig = '';
      if (+arr[1] + prec > 0) {
        sig = '+';
      }
      return (+(Math.round(+arr[0] + 'e' + sig + (+arr[1] + prec)) + 'e-' + prec)).toFixed(prec);
    }
  };

  // @todo: for IE parseFloat(0.55).toFixed(0) = 0;
  s = (prec ? toFixedFix(n, prec).toString() : '' + Math.round(n)).split('.');
  if (s[0].length > 3) {
    s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep);
  }
  if ((s[1] || '').length < prec) {
    s[1] = s[1] || '';
    s[1] += new Array(prec - s[1].length + 1).join('0');
  }

  return s.join(dec);
};

/**
 * Formats a number using site's current locale
 *
 * @see http://locutus.io/php/strings/number_format/
 * @param {Number|String} number number to format
 * @param {int|null} [precision=null] optional decimal precision
 * @returns {?String} A formatted string.
 */
export const numberFormat = (number, precision = null) => {
  if (typeof number !== 'number') {
    number = parseFloat(number);
  }

  if (isNaN(number)) {
    return '';
  }

  const decimalSeparator = wcSettings.currency.decimal_separator;
  const thousandSeparator = wcSettings.currency.thousand_separator;
  precision = parseInt(precision);

  if (isNaN(precision)) {
    const [, decimals] = number.toString().split('.');
    precision = decimals ? decimals.length : 0;
  }

  return numberDecimalFormat(number, precision, decimalSeparator, thousandSeparator);
};
