import React from 'react';

import { Box, Spinner } from '@lapilli-ui/components';
import parse from 'html-react-parser';

import { addToCartClasses } from '../../common/config';
import { getProductsToShow } from '../../common/utils/data';
import ProductList from '../elements/product-list';

const FrequentlyBoughtTogether = ({ campaign, productID, productPrice = 0, enabled = true }) => {
  if (!campaign || typeof campaign === 'undefined') return '';
  const options = campaign.meta[campaign.type];
  const {
    title,
    num_products_to_show: numProductToShow,
    products_to_show: productToShow,
    show_total: showTotal,
    discount_to_apply: discountToApply
  } = options;

  const skipMaxNumber = productToShow.type === 'products';
  const triggerProduct = { type: 'products', ids: [productID] };
  const { data: allProducts, isLoading, isFetching } = getProductsToShow(productToShow, triggerProduct, 'edit');

  const inFetching = isLoading || isFetching;

  const productListOPT = {
    productType: productToShow.type,
    applyDiscount: options.apply_discount,
    discountValue: discountToApply.discount_value,
    discountType: discountToApply.discount_type,
    addToCartLabel: options.add_to_cart_button_label,
    checkedByDefault: options.checked_by_default,
    borderColor: options.item_border_color,
    backgroundColor: options.item_background_color,
    borderRadiusItem: options.border_radius_item,
    showProductName: options.show_product_name,
    showProductPrice: options.show_product_price,
    showSaving: options.show_saving,
    savingColor: options.saving_color,
    addDiscount: options.apply_discount,
    checkboxColor: options.checkbox_color
  };

  return (
        <Box className="yith-sales-fbt-preview-wrapper" sx={{ maxWidth: '450px', paddingTop: '20px', clear: 'both' }}>
            <Box>{parse(title)}</Box>
            {inFetching
              ? (
                    <Box sx={{ textAlign: 'center' }}><Spinner/></Box>
                )
              : (
                    <>

                        <input type='hidden' name='yith_sales_fbt' value={campaign.id}/>
                        <input type='hidden' name='add-to-cart' value={productID}/>
                        <ProductList
                            context='frontend'
                            products={allProducts.filter(product => product.id !== productID)}
                            productOpts={productListOPT}
                            basePrice={productPrice}
                            showTotals={showTotal === 'yes'}
                            showNoThanks={false}
                            numProductToShow={skipMaxNumber ? -1 : numProductToShow}
                            addToCartClassName={addToCartClasses}
                            canAddToCart={enabled}
                            isAjax={false}
                        />
                    </>
                )}
        </Box>
  );
};

export default FrequentlyBoughtTogether;
