import React, { forwardRef } from 'react';

import { alpha, generateComponentClasses, styled } from '@lapilli-ui/styles';
import classNames from 'classnames';

const useComponentClasses = (ownerState) => {
  const { absolute, children, flexItem, light, orientation, textAlign, variant } =
        ownerState;
  return generateComponentClasses(
    'WonderCartDivider',
    {
      root: [
        'root',

        absolute && 'absolute',
        variant,
        light && 'light',
        orientation === 'vertical' && 'vertical',
        flexItem && 'flexItem',
        children && 'withChildren',
        children && orientation === 'vertical' && 'withChildrenVertical',
        textAlign === 'right' && orientation !== 'vertical' && 'textAlignRight',
        textAlign === 'left' && orientation !== 'vertical' && 'textAlignLeft'
      ],
      wrapper: [
        'wrapper',
        ownerState.orientation === 'vertical' && 'wrapperVertical'
      ]
    }
  );
};

const DividerRoot = styled('div', {
  name: 'WonderCartDivider',
  slot: 'Root',
  overridesResolver: (props, styles) => {
    const { ownerState } = props;

    return [
      styles.root,
      ownerState.absolute && styles.absolute,
      styles[ownerState.variant],
      ownerState.light && styles.light,
      ownerState.orientation === 'vertical' && styles.vertical,
      ownerState.flexItem && styles.flexItem,
      ownerState.children && styles.withChildren,
      ownerState.children && ownerState.orientation === 'vertical' && styles.withChildrenVertical,
      ownerState.textAlign === 'right' &&
            ownerState.orientation !== 'vertical' &&
            styles.textAlignRight,
      ownerState.textAlign === 'left' &&
            ownerState.orientation !== 'vertical' &&
            styles.textAlignLeft
    ];
  }
})(
  ({ theme, ownerState }) => ({
    margin: 0, // Reset browser default style.
    flexShrink: 0,
    borderWidth: 0,
    borderStyle: 'solid',
    borderColor: theme.palette.border.primary,
    borderBottomWidth: ownerState.borderWidth,
    ...(ownerState.absolute && {
      position: 'absolute',
      bottom: 0,
      left: 0,
      width: '100%'
    }),
    ...(ownerState.light && {
      borderColor: alpha(theme.palette.border.primary, 0.08)
    }),
    ...(ownerState.variant === 'inset' && {
      marginLeft: 72
    }),
    ...(ownerState.variant === 'middle' &&
            ownerState.orientation === 'horizontal' && {
      marginLeft: theme.spacing(2),
      marginRight: theme.spacing(2)
    }),
    ...(ownerState.variant === 'middle' &&
            ownerState.orientation === 'vertical' && {
      marginTop: theme.spacing(1),
      marginBottom: theme.spacing(1)
    }),
    ...(ownerState.orientation === 'vertical' && {
      height: '100%',
      borderBottomWidth: 0,
      borderRightWidth: ownerState.borderWidth
    }),
    ...(ownerState.flexItem && {
      alignSelf: 'stretch',
      height: 'auto'
    })
  }),
  ({ ownerState }) => ({
    ...(ownerState.children && {
      display: 'flex',
      whiteSpace: 'nowrap',
      textAlign: 'center',
      border: 0,
      '&::before, &::after': {
        content: '""',
        alignSelf: 'center'
      }
    })
  }),
  ({ theme, ownerState }) => ({
    ...(ownerState.children &&
            ownerState.orientation !== 'vertical' && {
      '&::before, &::after': {
        width: '100%',
        borderTop: `${ownerState.borderWidth} solid ${theme.palette.border.primary}`
      }
    })
  }),
  ({ theme, ownerState }) => ({
    ...(ownerState.children &&
            ownerState.orientation === 'vertical' && {
      flexDirection: 'column',
      '&::before, &::after': {
        height: '100%',
        borderLeft: `${ownerState.borderWidth} solid ${theme.palette.border.primary}`
      }
    })
  }),
  ({ ownerState }) => ({
    ...(ownerState.textAlign === 'right' &&
            ownerState.orientation !== 'vertical' && {
      '&::before': {
        width: '90%'
      },
      '&::after': {
        width: '10%'
      }
    }),
    ...(ownerState.textAlign === 'left' &&
            ownerState.orientation !== 'vertical' && {
      '&::before': {
        width: '10%'
      },
      '&::after': {
        width: '90%'
      }
    })
  })
);

const DividerWrapper = styled('span', {
  name: 'WonderCartDivider',
  slot: 'Wrapper',
  overridesResolver: (props, styles) => {
    const { ownerState } = props;

    return [styles.wrapper, ownerState.orientation === 'vertical' && styles.wrapperVertical];
  }
})(({ theme, ownerState }) => ({
  display: 'inline-block',
  paddingLeft: `calc(${theme.spacing(1)} * 1.2)`,
  paddingRight: `calc(${theme.spacing(1)} * 1.2)`,
  ...(ownerState.orientation === 'vertical' && {
    paddingTop: `calc(${theme.spacing(1)} * 1.2)`,
    paddingBottom: `calc(${theme.spacing(1)} * 1.2)`
  })
}));

const Divider = forwardRef(
  function Divider (
    {
      className,
      variant = 'fullWidth',
      absolute = false,
      flexItem = false,
      light = false,
      orientation = 'horizontal',
      textAlign = 'center',
      borderWidth = 'thin',
      component = 'hr',
      role,
      children,
      ...props
    },
    ref
  ) {
    const ownerState = { variant, absolute, flexItem, orientation, textAlign, light, borderWidth, children };
    const classes = useComponentClasses(ownerState);
    const asComponent = ownerState.children ? 'div' : component;
    const componentRole = asComponent !== 'hr' ? 'separator' : role;
    return (
        <DividerRoot
            ref={ref}
            as={asComponent}
            className={ classNames(classes.root, className) }
            ownerState ={ownerState}
            role={componentRole}
            {...props}
        >
            { ownerState.children && (
                <DividerWrapper className={classes.wrapper} ownerState={ownerState}>{children}</DividerWrapper>
            )
            }
        </DividerRoot>
    );
  }
);

export default Divider;
