import React, { useEffect, useReducer, useState } from 'react';

import { Box, Stack, Typography, VStack } from '@lapilli-ui/components';
import { useTheme } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { cartURL } from '../../../common/config';
import { getFormattedTermName, getImage, getNoProductLabel, isEmpty } from '../../../common/utils/common';
import { getProductAttributes } from '../../../common/utils/data';
import { getDiscountedPrice } from '../../../common/utils/numbers';
import AddToCartProductListButton from '../add-to-cart-product-list-button';
import BoxWithInfo from '../box-with-info';
import Price from '../price';
import ProductImage from '../product-image';
import ProductVariations from '../product-variations';
import SaveDiscount from '../save-discount';
import SimpleButton from '../simple-button';

function init (initialState) {
  return { product: initialState.product, productAttributes: initialState.productAttributes };
};

function scanReducer (state, action) {
  switch (action.type) {
    case 'initProduct':
      return { ...state, product: { ...action.payload } };
    case 'initProductAttributes':
      return { ...state, productAttributes: [...action.payload] };
    case 'reset':
      return init(action.payload);
  }
}

const ProductSingleItem = ({
  context = 'admin',
  data,
  productOpts,
  showFreeLabel = false,
  addToCartClassName = '',
  showViewCartButton = false,
  onSubmit,
  handleNoThanks
}) => {
  if (!data || data.length === 0) {
    const noResultLabel = getNoProductLabel(productOpts.productType);
    return (
            <BoxWithInfo info={noResultLabel} justify='center' iconSize={20} icon='InformationCircleIcon'
                         bgColor='#fff' color='#000' borderColor="#cacaca" margin='0 auto'/>
    );
  }
  const _initializeDefaultStatus = () => {
    const product = data[0];
    const productAttributes = getProductAttributes(product);
    return { product, productAttributes };
  };
  const initialState = _initializeDefaultStatus();
  const [state, dispatch] = useReducer(scanReducer, initialState, init);
  const { product, productAttributes } = state;
  const [addToCartDisabled, setAddToCartDisabled] = useState(true);
  const image = getImage(product);
  const theme = useTheme();
  const {
    discountValue,
    discountType,
    showProductName,
    showProductPrice,
    showSaving,
    savingColor,
    applyDiscount,
    addToCartLabel
  } = productOpts;

  let addDiscount = applyDiscount === 'yes' && discountValue > 0;
  const showSave = addDiscount && showSaving === 'yes';
  let minPrice = '';
  let maxPrice = '';
  let currentPrice = parseFloat(product.regular_price);
  let stockInfo = product.availability;
  if (product.type === 'variable') {
    if (!product?.variationSelected || isEmpty(product.variationSelected)) {
      minPrice = product.prices.min;
      maxPrice = product.prices.max;
      addDiscount = false;
    } else {
      currentPrice = parseFloat(product.variationSelected.regular_price);
      stockInfo = product.variationSelected.availability;
    }
  }
  let discountedPrice = currentPrice;
  if (addDiscount) {
    discountedPrice = getDiscountedPrice(currentPrice, discountValue, discountType);
  }

  useEffect(() => {
    const canAddToCart = () => {
      let canAddToCart = true;
      if (product.type === 'variable') {
        if (product?.variationSelected && !isEmpty(product.variationSelected)) {
          const variation = product.variationSelected;
          if (!variation.is_purchasable || !variation.is_in_stock || !variation.variation_is_visible) {
            canAddToCart = false;
          }
        } else {
          canAddToCart = false;
        }
      } else {
        if (!product.purchasable || !product.is_in_stock || !product.is_visible) {
          canAddToCart = false;
        }
      }
      setAddToCartDisabled(!canAddToCart);
    };
    canAddToCart();
  }, [product]);
  const handleAddToCart = (e) => {
    e.preventDefault();
    const data = [];
    if (product) {
      const element = {
        product_id: product.id,
        variation_id: product.type === 'variable' && product?.variationSelected ? product.variationSelected.variationID : '',
        variations: product.type === 'variable' && product?.variationSelected ? product.variationSelected.selectedAttributes : [],
        quantity: 1
      };

      data.push(element);
    }
    if (typeof onSubmit === 'function') {
      onSubmit(e, data);
      dispatch({ type: 'reset', payload: initialState });
    }
  };

  const onFoundVariation = (productID, variation) => {
    if (product.id === productID) {
      const newProduct = { ...product };
      newProduct.variationSelected = variation;
      dispatch({ type: 'initProduct', payload: newProduct });
    }
  };

  const handleSelectAttributes = (productID, attributes) => {
    if (product.id === productID) {
      dispatch({ type: 'initProductAttributes', payload: attributes });
    }
  };
  return (
        <Box className="yith-sales-single-product-item product" sx={{ maxWidth: '240px', margin: '0 auto' }}>
            <ProductImage src={image.src} alt={image.alt} width="240px"/>
            {showProductName === 'yes' && (
                <Typography sx={{
                  fontSize: '1.1em',
                  lineHeight: '17px',
                  textAlign: 'center',
                  fontWeight: '700',
                  margin: '10px auto 5px',
                  color: theme.forms.text
                }}> {getFormattedTermName(product)}</Typography>
            )
            }
            <Stack className="yith-sales-product-list-prices" spacing={1} align='center' justify="center"
                   direction='row'>
                {
                    product.type === 'variable' && isEmpty(product.variationSelected) && showProductPrice === 'yes'
                      ? (
                            <Price
                                price={addDiscount ? discountedPrice : currentPrice}
                                min={addDiscount ? getDiscountedPrice(minPrice, discountValue, discountType) : minPrice}
                                max={addDiscount ? getDiscountedPrice(maxPrice, discountValue, discountType) : maxPrice}
                            />
                        )
                      : (
                          showFreeLabel
                            ? (
                                    <Typography component='span' sx={{
                                      fontSize: '1em',
                                      lineHeight: '22px',
                                      fontWeight: '500',
                                      color: theme.extra.freeColor,
                                      textTransform: 'uppercase'
                                    }}>
                                        {__('Free!', 'wonder-cart')}
                                    </Typography>
                              )
                            : (
                                showProductPrice === 'yes' &&
                                    <Price
                                        price={addDiscount ? discountedPrice : currentPrice}
                                        min={addDiscount ? getDiscountedPrice(minPrice, discountValue, discountType) : minPrice}
                                        max={addDiscount ? getDiscountedPrice(maxPrice, discountValue, discountType) : maxPrice}
                                    />
                              )
                        )
                }
                {
                    addDiscount && showProductPrice === 'yes' && (
                        <Price price={currentPrice} showDel={true}/>
                    )
                }

            </Stack>
            <Box sx={{ textAlign: 'center', margin: '10px auto' }}>
                {showSave && <SaveDiscount color={savingColor}
                                           saved={discountValue}
                                           discountType={discountType}/>
                }
                {
                    stockInfo.availability !== '' &&
                    <Typography className={`stock ${stockInfo.class}`}
                                sx={{ fontSize: '1em!important' }}>{stockInfo.availability}</Typography>
                }
            </Box>
            {product.type === 'variable' && !isEmpty(product.attributes) &&
                <ProductVariations product={product} defaultAttributes={productAttributes}
                                   onFoundVariation={onFoundVariation} onSelectAttributes={handleSelectAttributes}/>
            }
            <VStack align='center' sx={{ zIndex: '10', marginTop: '20px' }}>
                {!showViewCartButton
                  ? (
                        <>
                            <AddToCartProductListButton label={addToCartLabel} className={addToCartClassName}
                                                        context={context}
                                                        isDisabled={addToCartDisabled} onClick={handleAddToCart}/>
                            <SimpleButton
                                className="yith-sales-nothanks-button"
                                label={__('No, thanks', 'wonder-cart')}
                                onClick={handleNoThanks}
                                sx={{
                                  color: '#0079CA',
                                  fontSize: '1em',
                                  marginTop: '10px',
                                  display: 'block',
                                  '&:hover': {
                                    color: '#0079CA'
                                  }
                                }
                                }
                            />
                        </>
                    )
                  : (
                        <>
                            <SimpleButton href={cartURL}
                                          label={__('View cart & checkout', 'wonder-cart')}
                                          sx={{
                                            display: 'block',
                                            width: '100%',
                                            textAlign: 'center',
                                            textDecoration: 'none',
                                            marginBottom: '15px',
                                            backgroundColor: 'var(--wp--preset--color--primary)',
                                            borderRadius: 'var(--wp--custom--button-border-radius)',
                                            color: 'var(--wp--preset--color--base)',
                                            fontSize: 'var(--wp--preset--font-size--small)',
                                            paddingTop: '0.5rem',
                                            paddingRight: '1rem',
                                            paddingBottom: '0.5rem',
                                            paddingLeft: '1rem',
                                            '&:hover': {
                                              textDecoration: 'none',
                                              backgroundColor: 'var(--wp--preset--color--primary)',
                                              color: 'var(--wp--preset--color--base)'
                                            }
                                          }}/>
                            <SimpleButton
                                    onClick={handleNoThanks}
                                    label= {__('Keep Shopping', 'wonder-cart')}
                                    sx={{
                                      display: 'block',
                                      width: '100%',
                                      textAlign: 'center',
                                      textDecoration: 'none',
                                      marginBottom: '15px',
                                      backgroundColor: '#fff',
                                      border: '1px solid #707070',
                                      borderRadius: 'var(--wp--custom--button-border-radius)',
                                      color: '#707070',
                                      fontSize: 'var(--wp--preset--font-size--small)',
                                      paddingTop: '0.5rem',
                                      paddingRight: '1rem',
                                      paddingBottom: '0.5rem',
                                      paddingLeft: '1rem',
                                      '&:hover': {
                                        textDecoration: 'none',
                                        backgroundColor: '#707070',
                                        color: '#fff'
                                      }
                                    }}
                            />
                        </>
                    )
                }
            </VStack>
        </Box>
  );
};

export default ProductSingleItem;
