import React from 'react';

import { Box, Stack } from '@lapilli-ui/components';
import { styled, useTheme } from '@lapilli-ui/styles';
import parse from 'html-react-parser';

import { addToCartClasses } from '../../common/config';
import { getPopupOptions } from '../../common/utils/common';
import { getRandomProductToShow } from '../../common/utils/data';
import ProductList from '../elements/product-list';
import ProductSingleItem from '../elements/product-single-item';
import SimpleModal from '../elements/simple-modal';

const GiftWrap = styled(Stack)(({ theme }) => ({
  '& .yith-sales-gift-icon': {
    width: '50px',
    height: '50px',
    backgroundImage: 'url(' + theme.gift.giftIcon + ')',
    backgroundSize: 'contain'
  }
}));
const GiftOnCart = ({ campaignID, options, handleNoThanks, onSubmit, open, addedToCart }) => {
  const numProductToShow = options.product_to_offer.type === 'categories' ? options.num_products_to_show : -1;
  const maxProductToAdd = options.max_products_to_add;
  const { data, isLoading, isFetching } = getRandomProductToShow({
    type: options.product_to_offer.type,
    ids: options.product_to_offer.ids
  }, numProductToShow);
  const inFetching = isLoading || isFetching;
  const isSingle = !data || data.length < 2;
  const productListOPT = {
    checked_by_default: 'no',
    discountValue: '100',
    discountType: 'percentage',
    showProductName: options.show_product_name,
    showProductPrice: options.show_product_price,
    showSaving: 'no',
    savingColor: '',
    applyDiscount: 'yes',
    addToCartLabel: options.add_to_cart_button_label
  };
  const theme = useTheme();
  const modalOpts = getPopupOptions(options);
  const newModalConfig = {
    ...modalOpts,
    bgImageConfig: {
      backgroundImage: 'url(' + theme.gift.giftBg + ')',
      backgroundPosition: 'top',
      backgroundRepeat: 'no-repeat'
    }
  };
  let maxWidth = '336px';
  let modalContent = false;
  if (inFetching) return '';

  if (!isSingle) {
    maxWidth = '490px';
    modalContent = <ProductList
            products={data}
            productOpts={productListOPT}
            numProductToShow={numProductToShow}
            maxProductToSelect={maxProductToAdd}
            showNoThanks={true}
            context='frontend'
            handleNoThanks={handleNoThanks}
            addToCartClassName={addToCartClasses}
            onSubmit={onSubmit}
            isAjax={true}
            showAllDoneMessage={true}
            showFreeLabel={true}
            showViewCartButton={addedToCart}
        />;
  } else {
    modalContent = <ProductSingleItem
            data={data}
            productOpts={productListOPT}
            handleNoThanks={handleNoThanks}
            onSubmit={onSubmit}
            context='frontend'
            addToCartClassName={addToCartClasses}
            showFreeLabel={true}
            showViewCartButton={addedToCart}
        />;
  }
  return (
        <>
            <SimpleModal
                open={open}
                context='frontend'
                maxWidth={maxWidth}
                {...newModalConfig}
                onClose={handleNoThanks}
                scrollable={true}
            >
                <GiftWrap direction='column' align='center' spacing={2}>
                    <Stack direction={isSingle ? 'column' : 'row'} align={ isSingle ? 'center' : 'baseline'} spacing={2}
                           sx={{ width: isSingle ? 'auto' : '100%' }}>
                        <div className='yith-sales-gift-icon'></div>
                        <Box>{parse(options.title)}</Box>
                    </Stack>
                    {modalContent}
                </GiftWrap>
            </SimpleModal>
        </>
  );
};
export default GiftOnCart;
