import React, { useState } from 'react';

import { addQueryArgs } from '@wordpress/url';
import parse from 'html-react-parser';

import CategoryPromotion from '../../../blocks/elements/category-promotion';
import ProductPromotion from '../../../blocks/elements/product-promotion';
import SimpleModal from '../../../blocks/elements/simple-modal';
import { $, addToCartClasses, ajaxAction, ajaxNonce, ajaxURL } from '../../../common/config';
import { useModalSwitch } from '../../../common/utils/common';
import { getTermsByType } from '../../../common/utils/data';
const BuyXGetYWrapper = ({ campaign, openModal, onCloseModal }) => {
  if (!campaign) return '';
  const [currentModalOpened, { handleCloseModal }] = useModalSwitch([campaign], 'yith_sales_modal_buy-x-get-y', 30, false, 1);
  const options = campaign.meta[campaign.type];
  const {
    title,
    trigger_product: triggerProduct,
    products_to_offer: productsToOffer,
    add_to_cart_button_label: buttonLabel,
    x_y_promo_rule: promoRule
  } = options;

  const productToShow = productsToOffer.type === 'same' ? triggerProduct : productsToOffer;

  const { data, isFetching } = getTermsByType({ termType: productToShow.type, termIds: productToShow.ids, fetchWithEmptyTerms: false });
  const [addedToCart, setAddedToCart] = useState(false);
  if (isFetching) return;
  const content = parse(title);

  const productListOPT = {
    discountValue: promoRule.discount_value,
    discountType: promoRule.discount_type,
    showProductName: 'yes',
    showProductPrice: 'yes',
    showSaving: 'no',
    savingColor: false,
    applyDiscount: 'yes',
    addToCartLabel: buttonLabel,
    productType: productToShow.type
  };

  const action = campaign.type.replaceAll('-', '_');

  const onSubmit = (e, data) => {
    const formData = {
      campaign_id: campaign.id,
      products: {
        ...data
      }
    };
    const queryArgs = addQueryArgs(ajaxURL, {
      action: ajaxAction,
      security: ajaxNonce,
      yith_sales_action: 'add_' + action,
      yith_sales_data: formData
    });
    fetch(queryArgs).then((response) => {
      if (response.status !== 200) {
        throw new Error(response.statusText);
      }
      return response.json();
    }).then((data) => {
      if (data?.fragments) {
        setAddedToCart(true);
        $(document.body).trigger('added_to_cart', [data.fragments, data.cart_hash]);
      }
    }).catch((err) => {
      console.log(err.toString());
    });
  };
  return (
                  <SimpleModal open={currentModalOpened.id === campaign.id} onClose={handleCloseModal} context='frontend'>
                    {
                      productToShow.type === 'categories'
                        ? (
                              <CategoryPromotion data={data} content={content} buttonLabel={buttonLabel} />
                          )
                        : (
                              <ProductPromotion data={data} content={content} productOptions={productListOPT} handleNoThanks={handleCloseModal} onSubmit={onSubmit} showViewCartButton={addedToCart} addToCartClassName={addToCartClasses}/>
                          )
                    }
                  </SimpleModal>

  );
};

export default BuyXGetYWrapper;
