import React from 'react';

import parse from 'html-react-parser';

import Promotion from '../../../blocks/promotion';
import { useGetCampaignQuery } from '../../../common/api/campaign';
const PromotionBannerModule = ({ campaignID }) => {
  const { data: campaign, isLoading, isFetching } = useGetCampaignQuery(campaignID);

  const inFetching = isLoading || isFetching;

  if (inFetching) return '';

  const options = campaign.meta[campaign.type];

  const bannerOptions = {
    showButton: options.show_cta === 'yes',
    buttonLabel: options.cta_button_label,
    buttonLink: options.cta_button_url.url,
    buttonBgColor: options.background_promotion_button_color,
    buttonBgHoverColor: options.background_promotion_button_hover_color,
    buttonColor: options.color_promotion_button_text,
    buttonColorHover: options.color_promotion_button_hover_text,
    bgColor: options.banner_color,
    showBgImage: options.upload_bg_image,
    bgImageUrl: options.upload_bg_image_field !== undefined ? options.upload_bg_image_field.url : ''
  };

  return (
      <><Promotion {...bannerOptions} promotionTitle={parse(options.text_to_show_banner)} isBanner={true}/></>
  );
};

export default PromotionBannerModule;
