import React from 'react';

import { Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';

import Icon from '../../../common/icon';

const BoxInfoWrapper = styled('div')(({ ownerState, theme }) => ({
  margin: ownerState.margin || '10px 0 10px 0',
  padding: '20px',
  border: '1px',
  borderStyle: 'dotted',
  borderColor: ownerState.borderColor || '#BD9999',
  backgroundColor: ownerState.bgColor || '#F9EEEA',
  display: 'flex',
  alignItems: 'center',
  justifyContent: ownerState.justifyContent || 'start',
  minWidth: ownerState.minWidth,
  '& .lapilliUI-Typography__root': {
    color: ownerState.color || '#571515',
    fontStyle: 'italic',
    fontSize: ownerState.fontSize || `${theme.typography.body3.fontSize}!important`,
    flex: 1
  },
  '& .yith-sales-icon': {
    color: ownerState.color || '#571515',
    marginTop: '4px',
    marginRight: '10px'
  }
}));
const BoxWithInfo = ({ info, icon, margin, justifyContent, borderColor, bgColor, color, fontSize, iconSize = 20, minWidth = 'auto' }) => {
  const ownerState = { margin, justifyContent, borderColor, bgColor, color, fontSize, minWidth };
  return (
      <BoxInfoWrapper className="yith-sales-box-with--info" ownerState={ownerState}>
        <Icon icon={icon} size={iconSize}/>
        <Typography>{info}</Typography>
      </BoxInfoWrapper>
  );
};

export default BoxWithInfo;
