import React, { useEffect, useState } from 'react';

import { addQueryArgs } from '@wordpress/url';

import UpsellWrap from './upsell-wrap';
import { $, ajaxAction, ajaxNonce, ajaxURL } from '../../../common/config';
import { isEmpty } from '../../../common/utils/common';

const UpsellModule = ({ campaignID, productID, cartItemKey }) => {
  const [currentCampaignID, setCurrentCampaignID] = useState(campaignID);
  const [currentProductID, setCurrentProductID] = useState(productID);
  const [currentCartItemKey, setcurrentCartItemKey] = useState(cartItemKey);
  const [showModal, setShowModal] = useState(false);
  const [addedToCart, setAddedToCart] = useState(false);
  const [cartData, setCartData] = useState({});

  const handleShowModal = () => {
    !addedToCart && setShowModal(true);
  };
  const handleCloseModal = () => {
    setShowModal(false);
    setCurrentCampaignID(false);
    setCartData({});
  };

  useEffect(() => {
    if (currentCampaignID !== false) {
      handleShowModal();
    }
  }, [currentCampaignID]);
  useEffect(() => {
    const getNewCampaignID = () => {
      if (!addedToCart) {
        const queryArgs = addQueryArgs(ajaxURL, {
          action: ajaxAction,
          security: ajaxNonce,
          yith_sales_action: 'find_valid_campaign_details'
        });
        fetch(queryArgs)
          .then((response) => {
            if (response.status !== 200) {
              throw new Error(response.statusText);
            }
            return response.json();
          })
          .then((data) => {
            setCurrentCampaignID(data.data.campaignID);
            setCurrentProductID(data.data.productID);
            setcurrentCartItemKey(data.data.cartItemKey);
          })
          .catch((err) => {
            console.log(err.toString());
          });
      }
    };
    $(document.body).on('wc-blocks_added_to_cart', getNewCampaignID);
    $(document.body).on('added_to_cart', getNewCampaignID);

    return () => {
      $(document.body).off('wc-blocks_added_to_cart', getNewCampaignID);
      $(document.body).off('added_to_cart', getNewCampaignID);
    };
  }, []);

  useEffect(() => {
    if (!isEmpty(cartData)) {
      return () => { $(document.body).trigger('added_to_cart', [cartData.fragments, cartData.cart_hash]); };
    }
  }, [cartData]);
  if (currentCampaignID === false || currentProductID === false) return '';

  const onSubmit = (e, data) => {
    const formData = {
      cart_item_key: currentCartItemKey,
      campaign_id: currentCampaignID,
      products: {
        ...data
      }
    };
    const queryArgs = addQueryArgs(ajaxURL, {
      action: ajaxAction,
      security: ajaxNonce,
      yith_sales_action: 'add_upsell',
      yith_sales_data: formData
    });
    fetch(queryArgs)
      .then((response) => {
        if (response.status !== 200) {
          throw new Error(response.statusText);
        }
        return response.json();
      })
      .then((data) => {
        if (data?.fragments) {
          setAddedToCart(true);
          setCartData(data);
        } else {
          window.location.replace(data.redirect_to);
        }
      })
      .catch((err) => {
        console.log(err.toString());
      });
  };

  return (
        <>
            <UpsellWrap
                campaignID={currentCampaignID}
                productID={currentProductID}
                openModal={showModal}
                showViewCartButton={addedToCart}
                onCloseModal={handleCloseModal}
                onSubmit={onSubmit}
            />
        </>
  );
};

export default UpsellModule;
