import React, { useState } from 'react';

import { addQueryArgs } from '@wordpress/url';

import GiftOnCart from '../../../blocks/gift-on-cart';
import LastDeal from '../../../blocks/last-deal';
import { $, addToCartClasses, ajaxAction, ajaxNonce, ajaxURL } from '../../../common/config';
import { unserializeMeta } from '../../../common/utils/common';

const ModalCampaign = ({ open, campaign, handleOnClose, reloadCheckout = false }) => {
  const meta = unserializeMeta(campaign.meta);
  const options = meta[campaign.type];
  const [addedToCart, setAddedToCart] = useState(false);
  let modalContent = false;
  const action = campaign.type.replaceAll('-', '_');
  const onSubmit = (e, data) => {
    const formData = {
      campaign_id: campaign.id,
      products: {
        ...data
      }
    };
    const queryArgs = addQueryArgs(ajaxURL, {
      action: ajaxAction,
      security: ajaxNonce,
      yith_sales_action: 'add_' + action,
      yith_sales_data: formData
    });
    fetch(queryArgs).then((response) => {
      if (response.status !== 200) {
        throw new Error(response.statusText);
      }
      return response.json();
    }).then((data) => {
      if (data?.fragments) {
        setAddedToCart(true);
        $(document.body).trigger('added_to_cart', [data.fragments, data.cart_hash]);
      }
      if (reloadCheckout) {
        $(document.body).trigger('update_checkout');
      }
      if (data?.redirect_to) {
        window.location.href = data.redirect_to;
      }
    }).catch((err) => {
      console.log(err.toString());
    });
  };

  if (campaign.type === 'gift-on-cart') {
    modalContent =
        <GiftOnCart campaignID={campaign.id} options={options} handleNoThanks={handleOnClose} onSubmit={onSubmit} open={open} addedToCart={addedToCart}/>;
  }
  if (campaign.type === 'last-deal') {
    modalContent = <LastDeal campaignID={campaign.id} options={options} handleNoThanks={handleOnClose} onSubmit={onSubmit} open={open} addedToCart={addedToCart} addToCartClasses={addToCartClasses}/>;
  }
  return (
      <>
        {modalContent}
      </>
  );
};

export default ModalCampaign;
