import React, { useState } from 'react';

import { Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { getFormattedTermName, getImage, isEmpty } from '../../../common/utils/common';
import { getDiscountedPrice } from '../../../common/utils/numbers';
import AddToCartButton from '../add-to-cart-button';
import Price from '../price';
import ProductImage from '../product-image';
import ProductInModal from '../product-in-modal';
import SaveDiscount from '../save-discount';

const ProductStyle = styled(Stack)(({ theme, fullwidth }) => ({
  '&': {
    textAlign: 'center'
  },
  '& a': {
    textDecoration: 'none'
  }
}));
const Product = ({ product, campaignProps, context = 'admin', preview = false, showVariableOnModal = false, onModalAddToCartClick, showViewCartButton = false, onCloseModal }) => {
  const { id, showBadge, badgeText, badgeBackgroundColor, badgeColor, showProductName, applyDiscount, discountValue, discountType, showProductPrice, showSaving, showAddToCart, addToCartLabel } = campaignProps;
  const productImage = getImage(product, true);

  let minimumPrice = '';
  let maximumPrice = '';
  let discountedPriceMin = '';
  let discountedPriceMax = '';
  let discountedPrice = '';
  let showDel = false;
  if (!isEmpty(product.prices)) {
    minimumPrice = product.prices.min;
    maximumPrice = product.prices.max;
    discountedPriceMin = applyDiscount === 'yes' ? getDiscountedPrice(minimumPrice, discountValue, discountType) : minimumPrice;
    discountedPriceMax = applyDiscount === 'yes' ? getDiscountedPrice(maximumPrice, discountValue, discountType) : minimumPrice;
    showDel = applyDiscount === 'yes' && (discountedPriceMin !== minimumPrice || discountedPriceMax !== maximumPrice);
  } else {
    discountedPrice = applyDiscount === 'yes' ? getDiscountedPrice(product.regular_price, discountValue, discountType) : product.regular_price;
    showDel = discountedPrice !== product.regular_price;
  }

  const [showProductModal, setShowProductModal] = useState(false);

  const handleOnClick = (e) => {
    if (product.type === 'variable') {
      e.preventDefault();
      e.stopPropagation();
      setShowProductModal(!showProductModal);
    }
  };
  const handleCloseModal = () => {
    setShowProductModal(false);
    onCloseModal();
  };

  const cartLabel = product.type === 'variable' ? __('Select options', 'wonder-cart') : addToCartLabel;

  const productListOPT = {
    applyDiscount,
    discountValue,
    discountType,
    showProductName,
    showProductPrice,
    showSaving,
    savingColor: '',
    addDiscount: applyDiscount,
    checkboxColor: '',
    addToCartLabel,
    checkedByDefault: 'yes'
  };

  return (
      <ProductStyle key={product.id}>
        <a href={product.permalink}>
          <ProductImage src={productImage.src} alt={productImage.alt} width="200px"
                        badge={showBadge === 'yes' ? badgeText : false}
                        badgeBackgroundColor={badgeBackgroundColor} badgeColor={badgeColor}
                        badgeBorderRadius="3px"/>
          {showProductName === 'yes' && <Typography sx={{
            marginTop: '20px',
            marginBottom: '10px',
            fontSize: '14px',
            lineHeight: '17px',
            textAlign: 'center',
            fontWeight: '700'
          }}> {getFormattedTermName(product)}</Typography>}
          <Stack justify="center" direction="row" sx={{ margin: '0 auto 10px', gap: '5px' }}>
            {applyDiscount === 'yes' && <Price price={discountedPrice} min={discountedPriceMin} max={discountedPriceMax} showDel={false}/>}
            {showProductPrice === 'yes' && !(product.type === 'variable' && applyDiscount === 'yes') &&
                <Price price={product.regular_price} min={minimumPrice} max={maximumPrice} showDel={showDel}/>}
            {applyDiscount === 'yes' && showSaving === 'yes' &&
                <SaveDiscount saved={discountValue} discountType={discountType}/>}
          </Stack>
        </a>
        {showAddToCart === 'yes' && <AddToCartButton campaignID={id} context={context} loop={true} product={product} size="md" label={cartLabel} onClick={handleOnClick}/>}
        {product.type === 'variable' && showVariableOnModal && <ProductInModal productListOPT={productListOPT} product={product} opened={showProductModal} onCloseModal={handleCloseModal} onSubmit={onModalAddToCartClick} showViewCartButton={showViewCartButton}/>}
      </ProductStyle>
  );
};

export default Product;
