import React, { useEffect, useState } from 'react';

import { Box, Button, Collapse, Typography } from '@lapilli-ui/components';
import { useTheme } from '@lapilli-ui/styles';
import { __, _n } from '@wordpress/i18n';

import QuantityDiscountVariation from './quantity-discount-variation';
import { DiscountWrapper, QuantityDiscountItemContent, QuantityDiscountItemWrapper, QuantityDiscountVariationsWrapper } from './styled';
import Checkbox from '../../common/fields/checkbox';
import { getImage, isEmpty } from '../../common/utils/common';
import { getDiscountedPrice } from '../../common/utils/numbers';
import Price from '../elements/price';
import ProductImage from '../elements/product-image';
import SaveDiscount from '../elements/save-discount';

const QuantityDiscountItem = ({ product, rule, options, checked, onClickCheckbox, onUpdateList, errors, context = 'frontend' }) => {
  const {
    item_background_color: backgroundColor,
    item_border_color: borderColor,
    badge_background_color: badgeBackground,
    badge_text_color: badgeText,
    show_saving: showSaving,
    show_price_for_unit: showPriceForUnit,
    show_product_image: showImage,
    show_quantity_badge: showBadge,
    ribbon_background_color: ribbonBackground,
    ribbon_text_color: ribbonText
  } = options;
  const theme = useTheme();
  const getAttributes = () => {
    if (product.type === 'variable' && !isEmpty(product.attributes)) {
      const hasVariationsAttributes = product.attributes.filter(attribute => attribute.has_variations === true);
      const attributes = [];
      hasVariationsAttributes.forEach(attribute => {
        let defaultValue = 'any';

        const defaultAttr = product.default_attributes.filter(defaultAttr => defaultAttr.name === attribute.name);

        if (defaultAttr.length) {
          defaultValue = defaultAttr[0].option;
        }
        attributes.push({
          key: attribute.key,
          taxonomy: attribute.taxonomy,
          name: attribute.name,
          option: defaultValue
        });
      });
      return attributes;
    }
  };

  const { unit } = rule;
  const [variationsList, setVariationsList] = useState([{ id: 0, quantity: unit, selectedAttributes: getAttributes(), selectedVariation: false }]);
  const productImage = getImage(product, true);

  const discountValue = rule.discountType === 'fixed' ? parseFloat(rule.discount) / unit : rule.discount;
  const getProductDiscountedPrice = () => {
    if (product.type === 'variable') {
      const discountedPriceMin = getDiscountedPrice(product.prices.min, discountValue, rule.discountType);
      const discountedPriceMax = getDiscountedPrice(product.prices.max, discountValue, rule.discountType);
      return <Price price={0} min={discountedPriceMin} max={discountedPriceMax} showDel={false} suffix={__('/unit', 'wonder-cart')} textColor={ribbonText}/>;
    } else {
      const discountedPrice = getDiscountedPrice(product.regular_price, discountValue, rule.discountType);
      return <Price price={discountedPrice} showDel={false} suffix={__('/unit', 'wonder-cart')} textColor={ribbonText}/>;
    }
  };
  const saved = rule.discountType === 'fixed' ? product.regular_price - getProductDiscountedPrice() : rule.discount;

  const sum = variationsList.reduce((accumulator, variation) => {
    return accumulator + parseInt(variation.quantity);
  }, 0);

  useEffect(() => {
    checked && onUpdateList(variationsList);
  }, []);

  const updateList = (list) => {
    checked && onUpdateList(list);
    setVariationsList(list);
  };
  const disableAddVariations = () => {
    return sum >= unit;
  };

  const handleAddVariation = () => {
    if (unit - sum > 0) {
      updateList([...variationsList, { id: Date.now(), quantity: (unit - sum), selectedAttributes: getAttributes(), selectedVariation: false }]);
    }
  };

  const handleOnChangeQuantity = (e, variation) => {
    const newQuantity = parseInt(e.target.value);
    let list = [...variationsList];
    list = list.map(singleVar => singleVar.id === variation.id ? { ...variation, quantity: newQuantity } : singleVar);
    updateList(list);
  };

  const calculateTotalQty = (selectedVariation) => {
    return variationsList.reduce((accumulator, variation) => {
      if (selectedVariation.variationID === variation?.selectedVariation.variationID) {
        return accumulator + parseInt(variation.quantity);
      }
      return accumulator;
    }, 0);
  };
  const handleFoundVariation = (variationID, selectedVariation) => {
    const newList = variationsList.map(variation => {
      if (variation.id === variationID) {
        variation.selectedVariation = isEmpty(selectedVariation) ? false : selectedVariation;

        if (!isEmpty(variation.selectedVariation) && selectedVariation.is_in_stock && selectedVariation.stock_quantity !== null) {
          const totalSameVariation = calculateTotalQty(variation.selectedVariation) - variation.quantity;
          const rest = selectedVariation.stock_quantity - totalSameVariation;
          if (rest < variation.quantity) {
            variation.quantity = rest;
          }
        }
      }
      return variation;
    });

    updateList(newList);
  };
  const handleSelectAttributes = (variationID, attributes) => {
    const newList = variationsList.map(variation => {
      if (variation.id === variationID) {
        variation.selectedAttributes = attributes;
      }
      return variation;
    });
    updateList(newList);
  };

  return (
      <QuantityDiscountItemWrapper borderColor={borderColor} backgroundColor={backgroundColor}>
        <QuantityDiscountItemContent className="item-content" onClick={() => onClickCheckbox(rule, variationsList)}>
          <Box>
            <Checkbox checked={checked} onChange={() => onClickCheckbox(rule, variationsList)}
                      checkColor={theme.forms.checkColor} readOnly context={context}/>

          </Box>
          {showImage === 'yes' && <ProductImage src={productImage.src} alt={productImage.alt} width="55px" badge={showBadge === 'yes' ? unit : ''} badgeBorderRadius="10px" badgeBackgroundColor={badgeBackground} badgeColor={badgeText}/>}
          <Typography className="qty">{`${unit} ${_n('quantity', 'quantities', unit, 'wonder-cart')}`}</Typography>
          {(showSaving === 'yes' || showPriceForUnit === 'yes') &&
              <DiscountWrapper className={`discount-on-${product.type} ${checked ? 'discount-selected' : 'discount'}`} textColor={ribbonText} backgroundColor={ribbonBackground}>
                {showSaving === 'yes' && saved > 0 && <SaveDiscount discountType={rule.discountType} saved={saved} usePrecision={true}/>}
                {showPriceForUnit === 'yes' && getProductDiscountedPrice()}
              </DiscountWrapper>
          }
        </QuantityDiscountItemContent>
        {product.type === 'variable' && !isEmpty(product.attributes) &&
            <Collapse open={checked}>
            <QuantityDiscountVariationsWrapper className={`yith-sales-qty-variation-wrapper ${(checked && errors?.quantity === true) ? 'error' : ''}`}>
              { variationsList.map(variation =>
                  <QuantityDiscountVariation checked={checked} errors={errors} key={variation.id} product={product} variation={variation} available={parseInt(rule.unit - sum)}
                                             rule={rule} onSelectAttributes={handleSelectAttributes} onFoundVariation={handleFoundVariation} onChangeQuantity={handleOnChangeQuantity} calculateQty={calculateTotalQty}/>
              )}
              { rule.unit > 1 && <Button fullWidth={true} className="variation-button" onClick={handleAddVariation} variant="outlined" size="sm" disabled={disableAddVariations()}>{__('+ Set quantity for variations', 'wonder-cart')}</Button> }
            </QuantityDiscountVariationsWrapper>
            </Collapse>

        }
      </QuantityDiscountItemWrapper>

  );
};

export default QuantityDiscountItem;
