import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

import { isEmpty } from '../../utils/common';
import { yithSalesProductCategoriesApiFetch } from '../basic-fetch';

const restBase = 'products';

export const apiProductSlice = createApi({
  reducerPath: 'apiProduct',
  baseQuery: fetchBaseQuery({
    baseUrl: '/wc/store/v1',
    tagTypes: ['ProductTerm'],
    fetchFn: yithSalesProductCategoriesApiFetch
  }),
  endpoints: (builder) => ({
    getTerms: builder.query({
      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        const { termType, termIds } = args;
        const customQuery = `/${restBase}/${termType}/`;
        if (isEmpty(termIds)) {
          return { data: [] };
        }
        if (Array.isArray(termIds)) {
          let data = [];
          for (const i in termIds) {
            const newData = await fetchWithBQ(customQuery + termIds[i]);

            if (newData.error) return { error: newData.error };
            const data2 = newData.data;

            data = [...data, data2];
          }
          data = data.sort((a, b) => b.count - a.count);
          return { data };
        } else {
          const data = await fetchWithBQ(customQuery + termIds);

          if (data.error) return { error: data.error };
          return { data };
        }
      }
    }),
    searchTerms: builder.query({
      async queryFn (args, _queryApi, _extraOption, fetchWithBQ) {
        const { search, type, fetchWithEmptyString = true } = args;

        if (search === '' && !fetchWithEmptyString) {
          return { data: [] };
        }
        const query = `/${restBase}/${type}?per_page=0&search=${search}&_local=user`;

        let data = await fetchWithBQ(query);

        if (data.error) return { error: data.error };

        data = data.data.sort((a, b) => b.count - a.count);

        return { data };
      }

    })
  })
});

export const {
  useGetTermsQuery,
  useSearchTermsQuery
} = apiProductSlice;
