import React from 'react';

import { Box, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';
import parse from 'html-react-parser';

import { getPopupOptions } from '../../common/utils/common';
import { getRandomProductToShow } from '../../common/utils/data';
import Countdown from '../elements/countdown';
import ProductList from '../elements/product-list';
import ProductSingleItem from '../elements/product-single-item';
import SimpleModal from '../elements/simple-modal';

export const StyledTitle = styled('div')(({ theme, textAlign = 'left' }) => ({
  textAlign,
  marginBottom: '23px',
  '& img': {
    maxWidth: '100px',
    height: 'auto'
  },
  '& ul': {
    listStyle: 'disc'
  }
}));

const LastDeal = ({ options, handleNoThanks, onSubmit, open, addedToCart, addToCartClasses = '' }) => {
  const {
    title,
    num_products_to_show: numProductToShow,
    products_to_offer: productsToOffer,
    discount_to_apply: discount,
    show_product_name: showProductName,
    show_product_price: showProductPrice,
    show_saving: showSaving,
    apply_discount: applyDiscount,
    add_to_cart_button_label: addToCartLabel,
    show_countdown: showCountdown,
    countdown_value: countdownValue,
    countdown_type: countdownType
  } = options;

  const { data, isLoading, isFetching } = getRandomProductToShow({
    type: productsToOffer.type,
    ids: productsToOffer.ids
  }, numProductToShow);
  const inFetching = isLoading || isFetching;
  if (inFetching) return '';
  let modalContent = false;

  const isSingle = !data || data.length < 2;
  const maxWidth = isSingle ? '336px' : '490px';

  const productListOPT = {
    checked_by_default: 'no',
    discountValue: discount.discount_value,
    discountType: discount.discount_type,
    showProductName,
    showProductPrice,
    showSaving,
    applyDiscount,
    addToCartLabel,
    productType: productsToOffer.type
  };

  const newModalConfig = getPopupOptions(options);
  if (!isSingle) {
    modalContent = <ProductList
        products={data}
        productOpts={productListOPT}
        numProductToShow={numProductToShow}
        showNoThanks={true}
        context='frontend'
        handleNoThanks={handleNoThanks}
        addToCartClassName={addToCartClasses}
        onSubmit={onSubmit}
        isAjax={true}
        showViewCartButton={addedToCart}
    />;
  } else {
    modalContent = <ProductSingleItem
        data={data}
        productOpts={productListOPT}
        handleNoThanks={handleNoThanks}
        onSubmit={onSubmit}
        context='frontend'
        addToCartClassName={addToCartClasses}
        showViewCartButton={addedToCart}
    />;
  }
  return (
      <SimpleModal
          open={open}
          context='frontend'
          maxWidth={maxWidth}
          onClose={handleNoThanks}
          {...newModalConfig}
      >
        <Box sx={{ width: 'fit-content', margin: '0 auto' }}>
          <Box><Typography sx={{fontSize: '1.2rem',fontWeight:700}}>{parse(title)}</Typography></Box>
          {showCountdown === 'yes' &&
              <Stack sx={{textAlign: 'center'}}>
                <Typography variant='body2' sx={{ textAlign: 'center', fontWeight:'700'}}>{__('The offer ends in:', 'wonder-cart')}</Typography>
                <Countdown countdownValue={countdownValue} countdownType={countdownType} start={true} onEnd={handleNoThanks}/>
              </Stack>
          }
          {modalContent}
        </Box>
      </SimpleModal>
  );
};

export default LastDeal;
