import { createApi, fetchBaseQuery } from '@reduxjs/toolkit/query/react';

import { CAMPAIGNS_PER_PAGE, context } from '../../config';
import { serializeMeta, unserializeMeta } from '../../utils/common';
import { yithSalesApiFetch } from '../basic-fetch';

const restBase = 'campaigns';

const transformResponse = (response, meta) => {
  if (response.meta) {
    response.meta = unserializeMeta(response.meta);
  }
  return response;
};

const transformGeneralResponse = (response, meta) => {
  if (response.meta) {
    response.meta = unserializeMeta(response.meta);
  }
  return [...response, {
    totalCount: Number(meta.response.headers.get('X-WP-Total')),
    totalPages: Number(meta.response.headers.get('X-WP-TotalPages'))
  }];
};

export const apiSlice = createApi({
  reducerPath: 'apiCampaign',
  tagTypes: ['Campaign'],
  baseQuery: fetchBaseQuery({
    baseUrl: '/yith_sales/v1',
    fetchFn: yithSalesApiFetch
  }),

  endpoints: (builder) => ({
    fetchCampaigns: builder.query({
      keepUnusedDataFor: 5,
      query: ({
        campaignType = 'all',
        status = 'publish',
        campaign_status = 'all',
        session = 0,
        perPage = CAMPAIGNS_PER_PAGE,
        page = 1
      }) => {
        const queryString = new URLSearchParams();
        if (campaignType !== 'all') {
          queryString.append('type', campaignType);
        }
        if (status !== 'any') {
          queryString.append('status', status);
        }
        if (campaign_status !== 'all') {
          queryString.append('campaign_status', campaign_status);
        }
        if (session !== 0) {
          queryString.append('session', session);
        }

        queryString.append('per_page', perPage);

        queryString.append('orderby', 'modified');

        page > 0 && queryString.append('page', page);
        queryString.append('context', 'edit');
        return `/${restBase}?${queryString.toString()}`;
      },
      transformResponse: transformGeneralResponse,
      providesTags: (result) =>
        result
          ? [
              ...result.map(({ id }) => ({ type: 'Campaign', id })),
              { type: 'Campaign', id: 'LIST' }
            ]
          : [{ type: 'Campaign', id: 'LIST' }]

    }),
    getCampaign: builder.query({
      query: (post_id) => {
        if (post_id === 'new' || !isNaN(post_id)) {
          if (post_id === 'new') {
            return { data: {}, isFetching: false };
          } else {
            return ({
              url: `/${restBase}/${post_id}?context=${context}`
            });
          }
        } else {
          return {};
        }
      },

      providesTags: (result, error, id) => [{ type: 'Campaign', id }],
      transformResponse
    }),
    getCampaigns: builder.query({
      query (post_ids) {
        return ({
          url: `/${restBase}?include=${post_ids}`
        });
      },
      providesTags: (result, error, id) => [{ type: 'Campaign', id }],
      transformResponse
    }),
    addCampaign: builder.mutation({
      query: (body) => ({
        url: `${restBase}/`,
        method: 'POST',
        body
      }),
      invalidatesTags: [{ type: 'Campaign', id: 'LIST' }],
      transformResponse
    }),
    saveCampaign: builder.mutation({
      query: ({ body, id, cache = false }) => {
        if (body.meta) {
          body.meta = serializeMeta(body.meta);
        }
        return ({
          url: `${restBase}/${id}/?_locale=user`,
          method: 'POST',
          body
        });
      },
      transformResponse,
      invalidatesTags: (result, error, arg) => {
        const { id } = result;
        const { cache = false } = arg;
        if (cache) return [];
        return ([{ type: 'Campaign', id }]);
      }
    }),
    deleteCampaign: builder.mutation({
      query: ({ id }) => {
        return ({
          url: `${restBase}/${id}/?_locale=user&force=true`,
          method: 'DELETE'
        });
      },
      invalidatesTags: ['Campaign']
    }),
    cloneCampaign: builder.mutation({
      query: ({ id }) => {
        return ({
          url: `${restBase}/clone/${id}/?_locale=user`,
          method: 'POST'
        });
      },
      transformResponse,
      invalidatesTags: ['Campaign']
    }),
    getCampaignStatistics: builder.query({
      query: () => {
        return `/${restBase}/statistics`;
      },
      providesTags: (result, error, id) => [{ type: 'Campaign', id }]
    }),
    registerEvent: builder.mutation({
      query: ({body} ) => {
        return ({
          url: `/${restBase}/register-event/`,
          method: 'POST',
          body
        });
      },
      providesTags: (result, error, id) => [{ type: 'Campaign', id }]
    }),
  })
});

export const {
  useFetchCampaignsQuery,
  useAddCampaignMutation,
  useGetCampaignsQuery,
  useSaveCampaignMutation,
  useGetCampaignQuery,
  useDeleteCampaignMutation,
  useCloneCampaignMutation,
  useGetCampaignStatisticsQuery,
  useRegisterEventMutation
} = apiSlice;
