import React, { useEffect, useState } from 'react';

import FrequentlyBoughtTogether from '../../../blocks/frequently-bought-together';
import { useGetCampaignQuery } from '../../../common/api/campaign';
import { $, simpleAddToCartSelector, variableAddToCartSelector } from '../../../common/config';

const FrequentlyBoughtTogetherModule = ({ campaignID, productID, productPrice }) => {
  const { data: campaign, isFetching: campaignIsFetching } = useGetCampaignQuery(campaignID);
  const [enabled, setEnabled] = useState(false);
  const [mainProductPrice, setMainProductPrice] = useState(productPrice);
  useEffect(() => {
    let addToCart = document.querySelector(simpleAddToCartSelector);
    if (!addToCart) {
      addToCart = document.querySelector(variableAddToCartSelector);
    }
    if (addToCart) {
      addToCart.style.display = 'none';
    }
  }, []);

  useEffect(() => {
    const form = document.querySelector('form.variations_form');
    if (form) {
      const enableCampaign = (event, variation, purchasable) => {
        const price = variation?.yith_sales_display_price ?? variation.display_price;
        setMainProductPrice(price);
        setEnabled(purchasable);
      };
      const resetCampaign = () => {
        setMainProductPrice(productPrice);
        setEnabled(false);
      };
      $(form).on('show_variation', enableCampaign);
      $(form).on('reset_data', resetCampaign);
    } else {
      setEnabled(true);
    }
  }, []);

  return (<>{!campaignIsFetching && campaign &&
        <FrequentlyBoughtTogether campaign={campaign} productID={productID} productPrice={mainProductPrice}
                                  enabled={enabled}/>}</>);
};

export default FrequentlyBoughtTogetherModule;
