import React, { useEffect, useState } from 'react';

import { styled } from '@lapilli-ui/styles';

import VariationAttribute from './variation-attribute';
import { isEmpty } from '../../../common/utils/common';

const VariationWrapper = styled('div')(({ theme, margin, color, fontSize }) => ({
  margin: margin || '10px 0',
  color: color || theme.forms.text,
  fontSize: fontSize || '13px',
  display: 'flex',
  flexDirection: 'column',
  rowGap: '8px',
  '& .variation-attribute': {
    display: 'flex',
    alignItems: 'center'
  },
  '& label': {
    color: color || theme.forms.text,
    margin: '0!important',
    fontSize: fontSize || '13px',
    fontWeight: '700',
    textTransform: 'capitalize',
    width: '80px'
  },
  '& .yith-sales-attribute-value': {
    width: '150px',
    height: '28px'
  },

  '& [role*="combobox"]': {
    borderColor: `${theme.forms.borderColor}!important`,
    backgroundColor: `${theme.forms.backgroundColor}!important`,
    color: `${theme.forms.text}!important`,
    borderRadius: `${theme.forms.borderRadius}px!important`,
    fontSize: '12px'
  }
}));
const ProductVariations = ({ product, defaultAttributes = [], onSelectAttributes, onFoundVariation, name = '' }) => {
  const attributes = product.attributes;
  const variations = product.variations;

  if (isEmpty(attributes)) return '';
  const hasVariationsAttributes = attributes.filter(attribute => attribute.has_variations === true);
  if (isEmpty(hasVariationsAttributes)) return '';
  const [attributesToShow, setAttributesToShow] = useState([]);
  const [attributeConfiguration, setAttributeConfiguration] = useState([]);
  const selectedAttributes = defaultAttributes;
  const [variationID, setVariationID] = useState('');
  const getValidDropDownOptions = (dropDownOption, validAttribute) => {
    const tmp = dropDownOption.map((option) => {
      const tmpOpt = { ...option };
      if (validAttribute.key === tmpOpt.key) {
        const tmpTerms = [...tmpOpt.terms];
        const terms = tmpTerms.map(term => {
          const tmpTerm = { ...term };

          if ((validAttribute.value === null || validAttribute.value === tmpTerm.slug)) {
            tmpTerm.attached = true;
          }
          return tmpTerm;
        });
        tmpOpt.terms = [...terms];
      }
      return tmpOpt;
    });

    return tmp;
  };
  const sortDropDownOptions = (dropDownOptions) => {
    const sortedDropDownOption = hasVariationsAttributes.map(variationsAttr => {
      const element = dropDownOptions.filter(dropDownOption => dropDownOption.key === variationsAttr.key);
      return element[0];
    });
    return sortedDropDownOption;
  };
  const getDropDownOptions = (attributeKey, validDropDownOptions) => {
    const validTerms = validDropDownOptions.filter(validAttr => validAttr.key === attributeKey);
    if (validTerms) {
      const options = validTerms.map(validTerm => {
        const newElement = {
          key: attributeKey,
          taxonomy: validTerm.taxonomy,
          name: validTerm.name
        };
        const terms = validTerm.terms.filter(term => term.attached === true);
        newElement.terms = [...terms];
        return newElement;
      });
      return options[0];
    }
    return [];
  };

  useEffect(() => {
    const initAttributesToShow = () => {
      let sortedAttributes = sortDropDownOptions(selectedAttributes);
      let configEnd = false;
      let dropDownOptions = [];
      do {
        configEnd = true;
        dropDownOptions = [];

        for (const index in sortedAttributes) {
          const selectedAttribute = sortedAttributes[index];
          const checkAttributes = sortedAttributes.map(attr => {
            const newAttr = { ...attr };
            if (newAttr.key === selectedAttribute.key) {
              newAttr.option = 'any';
            }
            return newAttr;
          });

          const variations = findMatchingVariations(checkAttributes);

          let dropDownOptionsForAttributes = [...hasVariationsAttributes];
          variations.forEach(variation => {
            variation.attributes.forEach(matchingAttribute => {
              dropDownOptionsForAttributes = getValidDropDownOptions(dropDownOptionsForAttributes, matchingAttribute);
            });
          });
          const singleOpt = getDropDownOptions(selectedAttribute.key, dropDownOptionsForAttributes);

          if (selectedAttribute.option !== 'any') {
            const hasVale = singleOpt.terms.filter(optTerm => optTerm.slug === selectedAttribute.option).shift();

            if (hasVale === undefined) {
              sortedAttributes = [...checkAttributes];
              configEnd = false;
              break;
            }
          }
          dropDownOptions.push(singleOpt);
        }
      } while (!configEnd);

      setAttributeConfiguration(sortedAttributes);
      setAttributesToShow([...dropDownOptions]);
    };
    initAttributesToShow();
  }, [selectedAttributes]);

  const findMatchingVariations = (attributes) => {
    const matching = [];

    variations.forEach(variation => {
      const variationAttributes = variation.attributes;
      let isMatch = true;
      for (const index in variationAttributes) {
        const variationAttribute = variationAttributes[index];
        const attrToCheck = attributes.filter(attr => attr.key === variationAttribute.key).shift();

        if (attrToCheck.option !== undefined && variationAttribute.value !== undefined && attrToCheck.option !== 'any' && variationAttribute.value !== null && attrToCheck.option !== variationAttribute.value) {
          isMatch = false;
        }
      }
      if (isMatch) {
        matching.push(variation);
      }
    });
    return matching;
  };

  const getAttributeValue = (attribute) => {
    let value = attributeConfiguration.filter(selectedAttribute => selectedAttribute.key === attribute.key);
    value = value.length ? value[0].option : 'any';

    return value;
  };

  const setAttributeAsSelect = (value, attribute) => {
    const oldAttributes = selectedAttributes.filter(selectAttr => selectAttr.key !== attribute.key);
    const newElement = { taxonomy: attribute.taxonomy, name: attribute.name, option: value, key: attribute.key };
    findVariation([...oldAttributes, newElement]);
    if (typeof onSelectAttributes === 'function') {
      onSelectAttributes(product.id, [...oldAttributes, newElement]);
    }
  };

  const findVariation = (attributes) => {
    let variation = {};
    for (const i in variations) {
      const variationAttributes = variations[i].attributes;
      let findVariation = true;
      for (const index in variationAttributes) {
        const variationAttribute = variationAttributes[index];
        const tmp = attributes.filter(attr => attr.name === variationAttribute.name && attr.option !== 'any' && (variationAttribute.value === null || variationAttribute.value === attr.option));
        if (tmp.length === 0) {
          findVariation = false;
          break;
        }
      }
      if (findVariation) {
        variation = {
          variationID: variations[i].id,
          selectedAttributes: attributes,
          ...variations[i].data
        };
        break;
      }
    }

    const newVariationID = isEmpty(variation) ? '' : variation.variationID;
    setVariationID(newVariationID);
    if (typeof onFoundVariation === 'function') {
      onFoundVariation(product.id, variation);
    }
    return variation;
  };

  return (
        <VariationWrapper className="yith-sales-attributes">
            {attributesToShow.map(attribute => {
              return (
                        <VariationAttribute
                            className="attributes"
                            attribute={attribute}
                            attributeValue={getAttributeValue(attribute)}
                            key={attribute.key}
                            onSelectAttribute={setAttributeAsSelect}
                            name={name}
                        />);
            }
            )
            }
            <input type='hidden' name={`${name}[variation_id]`} value={variationID}/>
        </VariationWrapper>
  );
};

export default ProductVariations;
