import { $ } from '../../../common/config';

const initCartDiscount = () => {
  const couponWrapper = document.getElementById('yith-sales-coupon-row-wrapper');
  if (couponWrapper) {
    const options = JSON.parse(couponWrapper.dataset.options);
    const couponRow = couponWrapper.parentNode.parentNode;

    couponRow.style.backgroundColor = options.bgColor;
    couponRow.style.color = options.textColor;
  }
};
$(document.body).on('updated_wc_div', initCartDiscount);

initCartDiscount();
