import React, { useEffect, useState } from 'react';

import { Box, Stack, Typography } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import { Checkbox } from '../../../common/fields';
import { getFormattedTermName, getImage, isEmpty } from '../../../common/utils/common';
import { getDiscountedPrice } from '../../../common/utils/numbers';
import Price from '../price';
import ProductImage from '../product-image';
import ProductVariations from '../product-variations';
import SaveDiscount from '../save-discount';

const ProductListItemStyled = styled('div')(({ ownerState, theme }) => ({
  '&': {
    borderRadius: ownerState.borderRadius || '7px',
    borderWidth: '1px',
    borderStyle: 'solid',
    borderColor: ownerState.borderColor || theme.palette.grey[100],
    backgroundColor: ownerState.backgroundColor || '#fff',
    marginBottom: '25px',
    padding: '15px',
    minWidth: '400px',
    '&.responsive-200 .yith-sales-product-list-header': {
      flexWrap: 'wrap'
    }
  },
  '& .stock': {
    fontSize: '0.95em!important'
  },
  '&.disabled': {
    pointerEvents: 'none',
    opacity: 0.5
  },
  '& .yithUI-Select__root': {
    width: '180px'
  },
  '& .yith-sales-product-list-item-content .lapilliUI-Typography_Root': {
    color: theme.forms.text
  }
}
));

const FreeLabel = styled('span')(({ theme }) => ({
  '&': {
    fontSize: '1em',
    lineHeight: '22px',
    fontWeight: '500',
    color: theme.extra.freeColor,
    textTransform: 'uppercase'
  }
}
));

const ProductListItem = ({
  className,
  product,
  productAttributes = [],
  options,
  showFreeLabel,
  checkedByDefault = false,
  canBeChecked = true,
  onSelectItem,
  onFoundVariation,
  onSelectAttributes,
  context,
  showCheckbox = true,
  name = ''
}) => {
  const {
    borderColor,
    backgroundColor,
    borderRadiusItem,
    discountValue,
    discountType,
    showProductName,
    showProductPrice,
    showSaving,
    savingColor,
    applyDiscount,
    checkboxColor
  } = options;

  const ownerState = {};
  const image = getImage(product);
  ownerState.backgroundColor = backgroundColor;
  if (borderRadiusItem) {
    const radius = Object.values(borderRadiusItem?.dimension).join('px ');
    ownerState.borderRadius = radius + 'px';
  }
  if (borderColor) {
    ownerState.borderColor = borderColor;
  }

  let addDiscount = applyDiscount === 'yes' && discountValue > 0;
  const showSave = addDiscount && showSaving === 'yes';
  let minPrice = '';
  let maxPrice = '';
  let currentPrice = parseFloat(product.regular_price);
  let stockInfo = product.availability;

  if (product.type === 'variable') {
    if (!product?.variationSelected || isEmpty(product.variationSelected)) {
      minPrice = product.prices.min;
      maxPrice = product.prices.max;
      addDiscount = false;
    } else {
      currentPrice = parseFloat(product.variationSelected.regular_price);
      stockInfo = product.variationSelected.availability;
    }
  }
  let discountedPrice = currentPrice;

  if (addDiscount) {
    discountedPrice = getDiscountedPrice(currentPrice, discountValue, discountType);
  }
  let savingLabel = discountValue;
  if (discountType === 'fixed' && currentPrice) {
    savingLabel = discountedPrice > 0 ? discountValue : currentPrice;
  }
  const [isChecked, setIsChecked] = useState(checkedByDefault);
  useEffect(() => {
    setIsChecked(checkedByDefault);
  }, [checkedByDefault]);

  const handleChangeCheckbox = (productID, isChecked) => {
    setIsChecked(isChecked);
    if (typeof onSelectItem === 'function') {
      onSelectItem(productID, isChecked);
    }
  };
  const disableClass = !canBeChecked ? 'disabled' : '';
  return (
        <ProductListItemStyled className={`yith-sales-product-list-item woocommerce ${className ?? ''} ${disableClass}`} ownerState={ownerState} >
          <input type="hidden" name={`${name}[product_id]`} value={product.id}/>
          <Stack direction="row" spacing={2} align="center" className='yith-sales-product-list-header'>
            <Stack direction="row" align="center" spacing={2}>
              {showCheckbox && <Box sx={{ minWidth: '24px' }}>
                <Checkbox checked={isChecked} onChange={() => handleChangeCheckbox(product.id, !isChecked)}
                          checkColor={checkboxColor} name={`${name}[selected]`}
                           context={context}/>
              </Box>
              }
              <Box sx={{ flex: 1 }}>
                <ProductImage src={image.src} alt={image.alt}/>
              </Box>
            </Stack>
            <Box className="yith-sales-product-list-item-content product">
              {
                  showProductName === 'yes' &&
                  <Typography
                      sx={{ fontWeight: 500, fontSize: '1em', lineHeight: '22px' }}>
                    {getFormattedTermName(product)}
                  </Typography>

              }
              <Stack direction="row" align="center" className="yith-sales-product-list-prices" spacing={1}>
                {
                  product.type === 'variable' && isEmpty(product.variationSelected) && showProductPrice === 'yes'
                    ? <Price
                          price={addDiscount ? discountedPrice : currentPrice}
                          min={addDiscount ? getDiscountedPrice(minPrice, discountValue, discountType) : minPrice}
                          max={addDiscount ? getDiscountedPrice(maxPrice, discountValue, discountType) : maxPrice}
                      />
                    : (showFreeLabel
                        ? <FreeLabel as="span">{__('Free!', 'wonder-cart')}</FreeLabel>
                        : (
                            showProductPrice === 'yes' &&
                                  <Price
                                      price={addDiscount ? discountedPrice : currentPrice}
                                      min={addDiscount ? getDiscountedPrice(minPrice, discountValue, discountType) : minPrice}
                                      max={addDiscount ? getDiscountedPrice(maxPrice, discountValue, discountType) : maxPrice}
                                  />
                          )
                      )
                }
                {addDiscount && showProductPrice === 'yes' && <Price price={currentPrice} showDel={true}/>}
                {showSave && <SaveDiscount color={savingColor} saved={savingLabel} discountType={discountType}/>}
              </Stack>
              {
                  stockInfo.availability !== '' &&
                  <Typography className={`stock ${stockInfo.class}`}
                              sx={{ fontSize: '0.95em' }}>{stockInfo.availability}</Typography>
              }
              { !className && product.type === 'variable' && !isEmpty(product.attributes) &&
                  <ProductVariations product={product} defaultAttributes={productAttributes}
                                     onSelectAttributes={onSelectAttributes} onFoundVariation={onFoundVariation}
                                     name={name}/>
              }
            </Box>
          </Stack>
          {className && product.type === 'variable' && !isEmpty(product.attributes) &&
              <ProductVariations product={product} defaultAttributes={productAttributes}
                                 onSelectAttributes={onSelectAttributes} onFoundVariation={onFoundVariation}
                                 name={name}/>
          }
        </ProductListItemStyled>

  );
};

export default ProductListItem;
