import React from 'react';

import { Stack } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import Link from '@mui/material/Link';

import { ButtonLinkStyled } from '../elements/styles/ButtonLink.styled';

const StackStyledWrapper = styled(Stack)(({ theme }) => ({
  '&.yith-sales-promotion--banner img': {
    height: '80px'
  },
  '&.yith-sales-promotion--popup': {
    color: '#000',
    maxWidth: '400px'
  },
  '& .yith-sales-promotion--text': {
    maxWidth: '90%',
    textAlign: 'center'
  },
  '&.yith-sales-promotion--popup .yith-sales-promotion--button': {
    marginBottom: '20px'
  }
}));
const Promotion = ({
  promotionTitle,
  showButton,
  buttonLink,
  buttonLabel,
  buttonColor,
  buttonBgColor,
  buttonColorHover,
  buttonBgHoverColor,
  bgColor = 'transparent',
  showBgImage = 'no',
  bgImageUrl = '',
  isBanner = true
}) => {
  const direction = isBanner ? 'row' : 'column';

  const bgImageSx = showBgImage === 'yes' ? { backgroundImage: 'url(' + bgImageUrl + ')', backgroundSize: 'cover', backgroundRepeat: 'no-repeat' } : {};
  return (
        <StackStyledWrapper className={`yith-sales-promotion yith-sales-promotion--${isBanner ? 'banner' : 'popup'}`}
                            direction={direction} align="center" justify="center" spacing={2} sx={{
                              backgroundColor: bgColor,
                              color: '#fff',
                              height: isBanner ? '110px' : 'auto',
                              margin: !isBanner ? '0 auto' : '0',
                              ...bgImageSx
                            }}>
            <Stack className="yith-sales-promotion--text" direction={direction} align="center"
                   justify="center" spacing={2}>
                {promotionTitle}
            </Stack>
            {
                showButton && (
                    <ButtonLinkStyled
                        className="yith-sales-promotion--button"
                        color={buttonColor}
                        bgcolor={buttonBgColor}
                        colorhover={buttonColorHover}
                        bgcolorhover={buttonBgHoverColor}
                    >
                        <Link href={buttonLink}>{buttonLabel}</Link>
                    </ButtonLinkStyled>
                )
            }
        </StackStyledWrapper>
  );
};

export default Promotion;
