import React, { forwardRef } from 'react';

import { IconButton, Paper } from '@lapilli-ui/components';
import { alpha, generateComponentClasses, styled } from '@lapilli-ui/styles';
import classNames from 'classnames';

import Icon from '../../../common/icon';

const useComponentClasses = (ownerState) => {
  const { variant, color, severity } = ownerState;
  const string = color || severity;
  return generateComponentClasses(
    'WonderCartAlert',
    {
      root: ['root', `${variant}${string.charAt(0).toUpperCase() + string.slice(1)}`, `${variant}`],
      icon: ['icon'],
      message: ['message'],
      action: ['action']
    }
  );
};
const generateUtilityClasses = (componentName, slots) => {
  const results = {};
  slots.forEach((slot) => {
    results[slot] = `${componentName}--${slot}`;
  });
  return results;
};
const alertClasses = generateUtilityClasses('WonderCartAlert', [
  'root',
  'action',
  'icon',
  'message',
  'filled',
  'filledSuccess',
  'filledInfo',
  'filledWarning',
  'filledError',
  'outlined',
  'outlinedSuccess',
  'outlinedInfo',
  'outlinedWarning',
  'outlinedError',
  'standard',
  'standardSuccess',
  'standardInfo',
  'standardWarning',
  'standardError'
]);

const AlertRoot = styled(Paper, {
  name: 'WonderCartAlert',
  slot: 'Root',
  overridesResolver: (props, styles) => {
    const { ownerState } = props;
    const string = ownerState.color || ownerState.severity;
    return [
      styles.root,
      styles[ownerState.variant],
      styles[`${ownerState.variant}${string.charAt(0).toUpperCase() + string.slice(1)}`]
    ];
  }
})(({ ownerState, theme }) => {
  const color = ownerState.color || ownerState.severity;
  return {
    ...theme.typography.body2,
    backgroundColor: 'transparent',
    display: 'flex',
    padding: '6px 16px',
    ...(color &&
            ownerState.variant === 'standard' && {
      color: alpha(theme.palette[color].light, 1),
      backgroundColor: alpha(theme.palette[color].main, 0.9),
      [`& .${alertClasses.icon}`]: {
        color: theme.palette[color].main
      }
    }),
    ...(color &&
            ownerState.variant === 'outlined' && {
      color: alpha(theme.palette[color].light, 0.6),
      border: `1px solid ${theme.palette[color].light}`,
      [`& .${alertClasses.icon}`]: {
        color: theme.palette[color].main
      }
    }),
    ...(color &&
            ownerState.variant === 'filled' && {
      fontWeight: theme.typography.h6.fontWeight,
      backgroundColor: theme.palette[color].main,
      color: theme.palette[color].contrastText
    })
  };
});

const AlertIcon = styled('div', {
  name: 'WonderCartAlert',
  slot: 'Icon',
  overridesResolver: (props, styles) => styles.icon
})(() => ({
  marginRight: 12,
  padding: '7px 0',
  display: 'flex',
  fontSize: 22,
  opacity: 0.9
}));

const AlertMessage = styled('div', {
  name: 'WonderCartAlert',
  slot: 'Message',
  overridesResolver: (props, styles) => styles.message
})(() => ({
  padding: '8px 0',
  minWidth: 0,
  overflow: 'auto'
}));

const AlertAction = styled('div', {
  name: 'WonderCartAlert',
  slot: 'Action',
  overridesResolver: (props, styles) => styles.action
})(() => ({
  display: 'flex',
  alignItems: 'flex-start',
  padding: '4px 0 0 16px',
  marginLeft: 'auto',
  marginRight: -8
}));

const defaultIconMapping = {
  success: <Icon icon="CheckCircleIconSolid" />,
  error: <Icon icon="ExclamationCircleSolid" />,
  info: <Icon icon="InformationCircleSolid" />,
  warning: <Icon icon="ExclamationTriangleSolid" />
};

const Alert = forwardRef(
  function Alert ({
    action,
    children,
    className,
    closeText = 'Close',
    color,
    components = {},
    componentsProps = {},
    icon,
    iconMapping = defaultIconMapping,
    onClose,
    role = 'alert',
    severity = 'success',
    slotProps = {},
    slots = {},
    variant = 'standard',
    ...other
  }, ref) {
    const ownerState = {
      color,
      severity,
      variant
    };
    const classes = useComponentClasses(ownerState);
    const AlertCloseButton = slots.closeButton ?? components.CloseButton ?? IconButton;
    const AlertCloseIcon = slots.closeIcon ?? components.CloseIcon ?? <Icon icon='XMarkIcon'/>;

    const closeButtonProps = slotProps.closeButton ?? componentsProps.closeButton;
    const closeIconProps = slotProps.closeIcon ?? componentsProps.closeIcon;

    return (
          <AlertRoot
              role={role}
              elevation={0}
              ownerState={ownerState}
              className={classNames(classes.root, className)}
              ref={ref}
              {...other}
          >
              {icon !== false
                ? (
                  <AlertIcon ownerState={ownerState} className={classes.icon}>
                      {icon || iconMapping[severity] || defaultIconMapping[severity]}
                  </AlertIcon>
                  )
                : null}
              <AlertMessage ownerState={ownerState} className={classes.message}>
                  {children}
              </AlertMessage>
              {action != null
                ? (
                  <AlertAction ownerState={ownerState} className={classes.action}>
                      {action}
                  </AlertAction>
                  )
                : null}
              {action == null && onClose
                ? (
                  <AlertAction ownerState={ownerState} className={classes.action}>
                      <AlertCloseButton
                          size="small"
                          aria-label={closeText}
                          title={closeText}
                          color="inherit"
                          onClick={onClose}
                          {...closeButtonProps}
                      >
                          <AlertCloseIcon fontSize="small" {...closeIconProps} />
                      </AlertCloseButton>
                  </AlertAction>
                  )
                : null}
          </AlertRoot>
    );
  }
);

export default Alert;
