import React from 'react';

import { Typography } from '@lapilli-ui/components';
import { styled, useTheme } from '@lapilli-ui/styles';

import { formatCurrency } from '../../../common/utils/numbers';

const PriceStyled = styled('span')(({ theme, fontcolor: fontColor }) => ({
  '&': {
    fontWeight: '700',
    fontSize: '1rem!important',
    lineHeight: '22px',
    color: fontColor
  },
  '& del': {
    fontSize: '1rem!important'
  }
}));
const Price = ({ price, min = '', max = '', showDel = false, suffix = '', textColor = false }) => {
  const theme = useTheme();
  let fontColor = textColor || theme?.extra?.saleColor;
  fontColor = showDel ? theme?.extra?.pricingColor : fontColor;

  let formattedPrice = formatCurrency(price);
  if (min > 0 && max > 0) {
    formattedPrice = (min !== max) ? `${formatCurrency(min)} - ${formatCurrency(max)}` : formatCurrency(min);
  }

  return (
        <PriceStyled className="yith-sales-price" fontcolor={fontColor}>
            {showDel && <del>{formattedPrice}<Typography className="yith-sales-price__suffix" component="span" sx={{ fontWeight: '400' }}>{suffix}</Typography></del>}
            { !showDel && <>{formattedPrice}<Typography className="yith-sales-price__suffix" component="span" sx={{ fontWeight: '400' }}>{suffix}</Typography></>}
        </PriceStyled>
  );
};

export default Price;
