import React from 'react';

import { Box, Stack } from '@lapilli-ui/components';

import ProductSingleItem from '../product-single-item';

const ProductPromotion = ({ data, productOptions, content, handleNoThanks, onSubmit, addToCartClassName, showViewCartButton, context = 'admin' }) => {
  return (
      <Stack direction='column' align='center' className="yith-sales-modal-campaign-product-promotion" spacing={ context === 'admin' ? 2 : 0}>
          <Box sx={{ marginTop: '20px' }}>{content}</Box>
          <ProductSingleItem data={data} productOpts={productOptions} handleNoThanks={handleNoThanks} onSubmit={onSubmit} showViewCartButton={showViewCartButton} addToCartClassName={addToCartClassName} context={context}/>
      </Stack>
  );
};

export default ProductPromotion;
