import { isEmpty } from './common';
import { useGetTermsQuery, useSearchTermsQuery } from '../api/products';
import { useGetProductsQuery, useGetProductsToShowQuery, useSearchProductsQuery } from '../api/yith-products';

export const getProductsToShow = (productTypeToShow, triggerProduct, context = 'edit') => {
  const queryArgs = {};

  switch (productTypeToShow.type) {
    case 'featured':
      queryArgs.searchFor = productTypeToShow.type;
      break;
    case 'best-seller':
      queryArgs.searchFor = productTypeToShow.type;
      break;
    case 'products':
      queryArgs.searchFor = productTypeToShow.type;
      queryArgs.termsIds = productTypeToShow.ids;
      queryArgs.fetchWithEmptyTerms = false;
      break;
    case 'cross-sell':
    case 'upsell':
    case 'related':
      queryArgs.searchForProp = productTypeToShow.type;
      queryArgs.termsType = triggerProduct.type;
      queryArgs.termsIds = triggerProduct.type === 'all' ? [] : triggerProduct.ids;
      queryArgs.fetchWithEmptyTerms = false;
      break;
  }
  queryArgs.context = context;
  return useGetProductsToShowQuery(queryArgs);
};

export const getRandomProductToShow = (triggerProduct, numberOfProducts = 1, exclusion = {}, context = 'edit') => {
  const queryArgs = {
    searchFor: triggerProduct.type !== 'categories' ? 'products' : '',
    termsIds: triggerProduct.type === 'all' ? [] : triggerProduct.ids,
    termsType: triggerProduct.type === 'categories' ? 'categories' : '',
    exclusion,
    numberOfProducts,
    context
  };

  return useGetProductsToShowQuery(queryArgs);
};

export const getTermsByType = (args) => {
  let data;
  if (args.termType === 'products') {
    data = useGetProductsQuery(args);
  } else {
    data = useGetTermsQuery(args);
  }
  return data;
};

export const searchTermsByType = (args) => {
  let data;
  if (args.type === 'products') {
    data = useSearchProductsQuery(args);
  } else {
    data = useSearchTermsQuery(args);
  }

  return data;
};

export const getProductAttributes = (product) => {
  const attributes = [];
  if (product.type === 'variable' && !isEmpty(product.attributes)) {
    const hasVariationsAttributes = product.attributes.filter(attribute => attribute.has_variations === true);

    hasVariationsAttributes.forEach(attribute => {
      let defaultValue = 'any';

      const defaultAttr = product.default_attributes.filter(defaultAttr => defaultAttr.name === attribute.name);

      if (defaultAttr.length) {
        defaultValue = defaultAttr[0].option;
      }
      attributes.push({
        key: attribute.key,
        taxonomy: attribute.taxonomy,
        name: attribute.name,
        option: defaultValue
      });
    });
  }
  return attributes;
};
