import React from 'react';

import parse from 'html-react-parser';

import SimpleModal from '../../../blocks/elements/simple-modal';
import Promotion from '../../../blocks/promotion';
import { getPopupOptions, isEmpty, unserializeMeta } from '../../../common/utils/common';
const PromotionModal = ({ open, campaign, handleOnClose }) => {
  const getModalConfiguration = (campaign) => {
    const meta = unserializeMeta(campaign.meta);
    const options = meta[campaign.type];
    const modalOptions = getPopupOptions(options, 'promotion');
    const newModalConfig = {
      modal: {
        ...modalOptions,
        bgImageConfig: options.upload_promotion_popup_bg_image === 'yes' && options.upload_bg_popup_image_field.url !== undefined
          ? {
              backgroundImage: 'url(' + options.upload_bg_popup_image_field.url + ')',
              backgroundSize: 'cover',
              backgroundRepeat: 'no-repeat'
            }
          : undefined
      },
      content: {
        text: options.text_to_show_popup,
        showButton: options.show_cta === 'yes',
        buttonLabel: options.cta_button_label,
        buttonLink: options.cta_button_url.url,
        buttonBgColor: options.background_promotion_button_color,
        buttonBgHoverColor: options.background_promotion_button_hover_color,
        buttonColor: options.color_promotion_button_text,
        buttonColorHover: options.color_promotion_button_hover_text
      },
      promotionText: {
        text: options.text_to_show_popup
      }
    };

    return newModalConfig;
  };

  const currentModal = getModalConfiguration(campaign);

  if (isEmpty(currentModal)) return '';
  return (
        <>
            <SimpleModal
                open={open}
                maxWidth='337px'
                context='frontend'
                {...currentModal.modal}
                onClose={handleOnClose}
                scrollable={true}
            >
                <Promotion {...currentModal.content} promotionTitle={parse(currentModal.promotionText.text)} isBanner={false}/>

            </SimpleModal>
        </>
  );
};
export default PromotionModal;
