import React from 'react';

import { Button } from '@lapilli-ui/components';
import { styled } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

const AddToCart = styled('a')(({ theme, fullwidth }) => ({
  width: fullwidth ? '100%' : 'auto',
  textAlign: 'center'
}));

const AddToCartAdm = styled('button', { shouldForwardProp: (prop) => prop !== 'fullWidth' })(({ theme, fullWidth }) => ({
  backgroundColor: 'var(--wp--preset--color--primary)',
  borderRadius: 'var(--wp--custom--button-border-radius)',
  borderWidth: 0,
  borderStyle: 'solid',
  color: 'var(--wp--preset--color--base)',
  fontSize: 'var(--wp--preset--font-size--small)',
  fontWeight: 400,
  paddingTop: '0.5rem',
  paddingRight: '1rem',
  paddingBottom: '0.5rem',
  paddingLeft: '1rem',
  marginBottom: '10px',
  width: fullWidth ? '100%' : 'auto'
}));

const AddToCartButton = ({ label, campaignID, fullWidth = true, size = 'lg', className = '', product, context = 'admin', loop = false, onClick }) => {
  const addToCartLabel = label || __('Add to cart', 'wonder-cart');

  return (
      <>
        { context === 'admin' && <AddToCartAdm size={size} className={`yith-sales-add-to-cart-button ${className}`} fullWidth={fullWidth}>{addToCartLabel}</AddToCartAdm>}
        { context !== 'admin' && <AddToCart className={`yith-sales-add-to-cart-button ${product.addToCartClasses}`} fullWidth={false} href={product.addToCartURL + '&campaign_id=' + campaignID} data-campaign_id={campaignID} data-product_id={product.id}
                                            data-product_sku={product.sku} data-quantity="1" onClick={onClick} >{addToCartLabel}</AddToCart>}
      </>

  );
};

export default AddToCartButton;
