import React from 'react';

import { styled } from '@lapilli-ui/styles';

const AddToCartList = styled('button', { shouldForwardProp: (prop) => prop !== 'fullWidth' })(({ theme, fullWidth }) => ({
  width: fullWidth ? '100%' : 'auto',
  textAlign: 'center',
  marginBottom: '10px',
  boxSizing: 'border-box',
  '&.yith-disabled': {
    opacity: '0.5',
    pointerEvents: 'none'
  }
}));

const AddToCartBtn = styled('button', { shouldForwardProp: (prop) => prop !== 'fullWidth' })(({ theme, fullWidth }) => ({
  backgroundColor: 'var(--wp--preset--color--primary)',
  borderRadius: 'var(--wp--custom--button-border-radius)',
  borderWidth: 0,
  borderStyle: 'solid',
  color: 'var(--wp--preset--color--base)',
  fontSize: 'var(--wp--preset--font-size--small)',
  fontWeight: 400,
  paddingTop: '0.5rem',
  paddingRight: '1rem',
  paddingBottom: '0.5rem',
  paddingLeft: '1rem',
  marginBottom: '10px',
  boxSizing: 'border-box',
  width: fullWidth ? '100%' : 'auto'
}));
const AddToCartProductListButton = ({ className, label, onClick, isDisabled = false, fullWidth = true, size = 'lg', context = 'context' }) => {
  const addToCartLabel = label;

  return (
    context !== 'frontend'
      ? (
              <AddToCartBtn size={size} fullWidth={fullWidth} className='wp-element-button' onClick={onClick}>{addToCartLabel}</AddToCartBtn>
        )
      : (
              <AddToCartList className={`yith-sales-add-to-cart-button ${className} ${isDisabled ? 'yith-disabled' : ''}`} fullWidth={fullWidth} onClick={onClick}>{addToCartLabel}</AddToCartList>
        )
  );
};

export default AddToCartProductListButton;
