import React from 'react';

import PromotionModalWrap from './promotion-modal-wrap';
import { useGetCampaignsQuery } from '../../../common/api/campaign';

const PromotionModalModule = ({ campaignIDS }) => {
  const { data, isLoading, isFetching } = useGetCampaignsQuery(campaignIDS);
  const inFetching = isLoading || isFetching;

  if (inFetching) return '';

  return (
        <>
          <PromotionModalWrap campaigns={data}/>
        </>
  );
};

export default PromotionModalModule;
