import React from 'react';

import {render} from '@wordpress/element';

import Badge from './badge';

document.addEventListener('DOMContentLoaded', function () {
  const badgeSelector = document.querySelectorAll('.yith-sales-badge');

  if (badgeSelector) {
    badgeSelector.forEach(badge => {
      let onSale = false;
      let gallery = false;
      const info = JSON.parse(badge.dataset.info);
      const isLoop = badge.closest('.products') || badge.closest('.products-block-post-template');
      if (isLoop) {
        const link = badge.closest('a') || badge.closest('.product').querySelector('div a');
        if (link) {
          onSale = link.querySelector('.onsale') || link.querySelector('.wc-block-components-product-sale-badge');
        }
      } else {
        const product = badge.closest('.product');
        if (product) {
          onSale = product.querySelector('.onsale');
          gallery = product.querySelector('.woocommerce-product-gallery');
          gallery.appendChild(badge);
        }
      }

      render(
          <React.StrictMode>
            <Badge badgeNode={badge} content={info.badgeText} onSaleNode={onSale} galleryNode={gallery}/>
          </React.StrictMode>,
          badge
      );
    });
  }
});
