import { styled } from '@lapilli-ui/styles';

export const VerticalScrollableContentStyled = styled('div', { shouldForwardProp: (prop) => prop !== 'maxHeight' && prop !== 'paddingRight' })(({
  theme,
  maxHeight,
  paddingRight = '0'
}) => ({
  paddingRight,
  maxHeight,
  overflowY: 'auto',
  overflowX: 'hidden',
  height: '100%',
  '&': {
    scrollbarWidth: 'thin',
    scrollbarColor: theme.palette.primary.main + ' ' + theme.palette.grey[50]
  },
  '&.with-scroll': {
    marginBottom: '40px',
    boxShadow: '1px 4px 4px -5px  rgba(0,0,0,0.25)'
  },
  '&::-webkit-scrollbar': {
    width: '7px'
  },
  '&::-webkit-scrollbar-track': {
    borderRadius: '3px',
    backgroundColor: theme.palette.grey[50]
  },
  ' &::-webkit-scrollbar-thumb': {
    borderRadius: '3px',
    backgroundColor: theme.palette.primary.main
  }
})
);
