import React, { useEffect, useState } from 'react';

import { Box, Select } from '@lapilli-ui/components';

import { getOptionFromTaxTerms, isEmpty } from '../../../common/utils/common';

const VariationAttribute = ({ attribute, attributeValue, onSelectAttribute, name = '' }) => {
  if (isEmpty(attribute)) return '';
  const options = getOptionFromTaxTerms(attribute.terms);

  const [value, setValue] = useState(attributeValue);
  useEffect(() => {
    setValue(attributeValue);
  }, [attributeValue]);

  const handleChangeAttribute = (newValue, attribute) => {
    setValue(newValue);
    if (typeof onSelectAttribute === 'function') {
      onSelectAttribute(newValue, attribute);
    }
  };
  const attributeTaxonomy = attribute.taxonomy === null ? attribute.name.toLowerCase() : attribute.taxonomy;
  return (
        <Box className="variation-attribute">
            <label htmlFor={attribute.taxonomy}>{attribute.name}</label>
            <Select
                size="sm"
                options={options}
                value={value}
                onChange={(value) => handleChangeAttribute(value, attribute)}
                id={attribute.taxonomy}
                classes={{ root: 'yith-sales-attribute-value select-smallest' }}
                name={`${name}[variation][attribute_${attributeTaxonomy}]`}
            />
        </Box>
  );
};

export default VariationAttribute;
