import React from 'react';

import { createTheme, ThemeProvider } from '@lapilli-ui/styles';
import { render } from '@wordpress/element';
import { CookiesProvider } from 'react-cookie';
import { Provider } from 'react-redux';

import PromotionBannerModule from './promotion-banner-module';
import PromotionModalModule from './promotion-modal-module';
import { buildInitialSettingState } from '../../../common/utils/common';
import { frontendStore } from '../../store';
import { getGlobalTheme, mainTheme } from '../../theme';

document.addEventListener('DOMContentLoaded', function () {
  const promotionsModalSelector = document.querySelector('.yith-sales-modal-promotions');
  const promotionBannerSelector = document.querySelector('.yith-sales-banner-promotion');

  if (promotionsModalSelector) {
    const options = JSON.parse(promotionsModalSelector.dataset.options);
    const settings = buildInitialSettingState();

    const globalThemeSettings = getGlobalTheme(settings);
    const globalTheme = createTheme({ ...mainTheme, ...globalThemeSettings });
    render(
            <React.StrictMode>
                <Provider store={frontendStore}>
                    <ThemeProvider theme={globalTheme}>
                        <CookiesProvider>
                            <PromotionModalModule {...options}/>
                        </CookiesProvider>
                    </ThemeProvider>
                </Provider>
            </React.StrictMode>,
            promotionsModalSelector
    );
  }

  if (promotionBannerSelector) {
    const options = JSON.parse(promotionBannerSelector.dataset.options);
    const settings = buildInitialSettingState();

    const globalThemeSettings = getGlobalTheme(settings);
    const globalTheme = createTheme({ ...mainTheme, ...globalThemeSettings });
    render(
          <React.StrictMode>
              <Provider store={frontendStore}>
                  <ThemeProvider theme={globalTheme}>
                      <CookiesProvider>
                          <PromotionBannerModule {...options}/>
                      </CookiesProvider>
                  </ThemeProvider>
              </Provider>
          </React.StrictMode>,
          promotionBannerSelector
    );
  }
});
