import React from 'react';

import Upsell from '../../../blocks/upsell';
import { useGetCampaignQuery } from '../../../common/api/campaign';

const UpsellWrap = ({ campaignID, productID, openModal, showViewCartButton, onCloseModal, onSubmit }) => {
  const { data: campaign, isFetching: campaignIsFetching } = useGetCampaignQuery(campaignID);

  if (campaignIsFetching) return '';

  return (
      <><Upsell campaign={campaign} productID={productID} openModal ={openModal} showViewCartButton={showViewCartButton} onCloseModal={onCloseModal} onSubmit={onSubmit}/> </>
  );
};

export default UpsellWrap;
