import React from 'react';

import ModalCampaignsWrap from './modal-campaigns-wrap';
import { useGetCampaignsQuery } from '../../../common/api/campaign';

const ModalCampaignsModule = ({ campaignIDS, reloadCheckout = false }) => {
  const { data, isLoading, isFetching } = useGetCampaignsQuery(campaignIDS);
  const inFetching = isLoading || isFetching;

  if (inFetching) return '';

  return (
      <>
          <ModalCampaignsWrap campaigns={data} reloadCheckout={reloadCheckout}/>
      </>
  );
};

export default ModalCampaignsModule;
