import { createTheme } from '@lapilli-ui/styles';
export const mainTheme = {
  palette: {
    success: {
      light: 'rgba(243, 253, 246)',
      main: 'rgba(131, 216, 140)',
      dark: 'rgba(131, 216, 140)',
      contrastText: '#fff'
    }
  }
};
export const theme = createTheme(mainTheme);

export const getGlobalTheme = (settings) => {
  const sets = settings['general-settings'];
  const notifications = settings['general-settings'].notifications;

  const global = {
    forms: {
      backgroundColor: sets.form.yith_sales_form_colors.background_color,
      borderColor: sets.form.yith_sales_form_colors.border_color,
      text: sets.form.yith_sales_form_colors.text_color,
      checkColor: sets.form.yith_sales_form_colors.check_color,
      borderRadius: sets.form.yith_sales_form_border.border_radius
    },
    badge: {
      backgroundColor: sets.badges.yith_sales_badge_colors.background_color,
      text: sets.badges.yith_sales_badge_colors.text_color
    },
    modal: {
      backgroundColor: sets.modal_window.yith_sales_modal_colors.background_color,
      closeIcon: sets.modal_window.yith_sales_modal_colors.close_icon_color,
      borderRadius: sets.modal_window.yith_sales_modal_border.border_radius,
      overlayColor: sets.modal_window.yith_sales_modal_colors.overlay_color
    },
    extra: {
      pricingColor: sets.extra.yith_sales_price_colors.regular_color,
      saleColor: sets.extra.yith_sales_price_colors.sale_color,
      savingColor: sets.extra.yith_sales_price_colors.saving_color,
      freeColor: sets.extra.yith_sales_price_colors.free_color
    },
    gift: {
      giftIcon: sets.gift_product.yith_sales_gift_settings.gift_icon.url,
      giftBg: sets.gift_product.yith_sales_gift_settings.gift_bg.url
    },
    freeShipping: {
      freeShippingIcon: sets.free_shipping.yith_sales_free_shipping_icon.url
    },
    notifications: {
      initialDelay: parseInt(notifications.yith_sales_notification_initial_delay.delay),
      hideNotification: notifications.yith_sales_hide_notification_after_time,
      hideNotificationAfter: parseInt(notifications.yith_sales_hide_notification_after.delay),
      timeBetween: parseInt(notifications.yith_sales_notification_time_between.time_between),
      waitAfterClose: parseInt(notifications.yith_sales_notification_wait_after_close.delay)
    }
  };

  return global;
};
