import React from 'react';

import parse from 'html-react-parser';

import QuantityDiscount from '../../../blocks/quantity-discount';
import { useGetCampaignQuery } from '../../../common/api/campaign';
import { addToCartClasses } from '../../../common/config';
import { getCampaignTitle } from '../../../common/utils/common';
const QuantityDiscountModule = ({ campaignID, productID }) => {
  const { data: campaign, isFetching: campaignIsFetching } = useGetCampaignQuery(campaignID);

  if (campaignIsFetching) return '';
  const campaignTitle = getCampaignTitle(campaign);
  const campaignTitleComponent = parse(campaignTitle);

  return (
      <React.Fragment>
        {!campaignIsFetching && campaign && <QuantityDiscount campaign={campaign} campaignTitle={campaignTitleComponent} preview={false} context="frontend" step={false} productID={productID} addToCartClasses={addToCartClasses}/>}
      </React.Fragment>
  );
};

export default QuantityDiscountModule;
