import React from 'react';

import { Typography } from '@lapilli-ui/components';
import { useTheme } from '@lapilli-ui/styles';
import { __, sprintf } from '@wordpress/i18n';

import { formatCurrency } from '../../../common/utils/numbers';
const SaveDiscount = ({ saved, discountType, color = false, usePrecision = false }) => {
  const theme = useTheme();
  const fontColor = color || theme?.extra?.savingColor;

  let saveFormatted = discountType === 'fixed' ? formatCurrency(saved, false, usePrecision) : saved + '%';
  saveFormatted = sprintf(__('Save %s', 'wonder-cart'), saveFormatted);

  return (
        <Typography className="yith-sales-save-discount" component="span" sx={{ fontWeight: '700', color: fontColor, fontSize: '0.7rem', lineHeight: '22px' }}>
            {saveFormatted}
        </Typography>
  );
};
export default SaveDiscount;
