import React from 'react';

import Badge from '@mui/material/Badge';

import { StyledImage } from './styled';

const ProductImage = ({ src, width, height, alt, badge, anchorOrigin = { horizontal: 'right', vertical: 'top' }, badgeColor, badgeBackgroundColor, badgeBorderRadius }) => {
  let component = false;
  if (badge) {
    component = <Badge badgeContent={badge} anchorOrigin={anchorOrigin} max={99999999}>
                <img src={src} alt={alt} width={width}/>
            </Badge>;
  } else {
    component = <img src={src} alt={alt}/>;
  }

  const ownerState = { width, height, badgeColor, badgeBackgroundColor, badgeBorderRadius };

  return (
        <StyledImage ownerState={ownerState} className="product-image" >
            {component}
        </StyledImage>
  );
};

export default ProductImage;
