import React from 'react';

import { Box, Stack, Typography } from '@lapilli-ui/components';
import { useTheme } from '@lapilli-ui/styles';
import { __ } from '@wordpress/i18n';

import Price from '../price';
import SaveDiscount from '../save-discount';

const ProductListTotals = ({
  totalPrices,
  totalDiscountedPrices,
  showDiscount,
  showSaving,
  savingColor
}) => {
  const theme = useTheme();
  return (
        <Box className="yith-sales-products-total" sx={{ marginTop: '23px', marginBottom: '17px' }}>
            <Stack direction='row' spacing={1} justify="center" align='center'>
                <Typography
                    sx={{ fontWeight: '700', lineHeight: '22px', fontSize: '15px', color: theme.forms.text }}>
                    {__('Total:', 'wonder-cart')}
                </Typography>
                <Price
                    price={showDiscount ? totalDiscountedPrices : totalPrices}
                />
                {showDiscount && (totalDiscountedPrices !== totalPrices) && (
                    <Price price={totalPrices} showDel={true}/>
                )
                }
                {showDiscount && showSaving === 'yes' && (totalDiscountedPrices !== totalPrices) && (
                    <SaveDiscount color={savingColor}
                                  saved={totalPrices - totalDiscountedPrices}
                                  discountType="fixed" usePrecision={true}/>
                )}
            </Stack>
        </Box>
  );
};

export default ProductListTotals;
